<template>
	<section>
		<!-- start-->
		<div class="risk-box" v-loading="summaryLoading">
			<el-row :gutter="20">
				<el-col :span="6">
					<div class="riskItem">
						<p class="pName"></p>
						<div class="name">{{summaryData.totalRuleNum}}</div>
						<img src="../../../static/images/rule-icon-1.png" class="riskIcon" alt="">
					</div>
				</el-col>
				<el-col :span="6">
					<div class="riskItem">
						<p class="pName"></p>
						<div class="name">{{summaryData.redLineRuleNum}}</div>
						<img src="../../../static/images/rule-icon-2.png" class="riskIcon" alt="">
					</div>
				</el-col>
				<el-col :span="6">
					<div class="riskItem">
						<p class="pName"></p>
						<div class="name">{{summaryData.transferRuleNum}}</div>
						<img src="../../../static/images/rule-icon-3.png" class="riskIcon" alt="">
					</div>
				</el-col>
				<el-col :span="6">
					<div class="riskItem">
						<p class="pName"></p>
						<div class="name">{{summaryData.avgTransferRuleNum}}</div>
						<img src="../../../static/images/rule-icon-4.png" class="riskIcon" alt="">
					</div>
				</el-col>
			</el-row>

		</div>
		<!-- end-->

		<!---->
		<el-row style="margin-top: 20px;">
			<el-form label-width="120px">
				<el-col :span="6">
					<el-form-item label="" :inline="true"> <el-input  placeholder="" v-model="filters.ruleType"></el-input></el-form-item>
				</el-col>

				<el-col :span="6">
					<el-form-item label="" :inline="true"> <el-input  placeholder="" v-model="filters.smallRuleType"></el-input></el-form-item>
				</el-col>

				<el-col :span="6">
					<el-form-item label="" :inline="true">
						<el-date-picker
								v-model="filters.dataRange"
								type="daterange"
								:picker-options="pickerOptions"
								placeholder=""
								:editable = false
								@change="getData"
								align="right">
						</el-date-picker>
					</el-form-item>
				</el-col>

				<el-col :span="6">
					<el-form-item class="btn_box">
						<el-button type="primary" icon="search" v-on:click="search()"></el-button>
					</el-form-item>
				</el-col>
			</el-form>
		</el-row>

		<el-row class="button_box">
			<el-button  icon="document" class="btn_border_none"  @click="handleDownload"></el-button>
		</el-row>

		<el-table :data="tableData" border highlight-current-row style="width: 100%" v-loading="listLoading">
			<el-table-column prop="statisticsDate" label="" width="130"  show-overflow-tooltip></el-table-column>
			<el-table-column prop="ruleType" label="" min-width="120" show-overflow-tooltip></el-table-column>
			<el-table-column prop="smallRuleType" label="" min-width="120" show-overflow-tooltip></el-table-column>
			<el-table-column prop="triggerNum" label="" min-width="80" show-overflow-tooltip></el-table-column>
			<el-table-column prop="totalTriggerNum" label="" min-width="80" show-overflow-tooltip></el-table-column>
			<el-table-column prop="triggerRate" label="" min-width="80" show-overflow-tooltip></el-table-column>
			<el-table-column prop="totalTriggerRate" label="" min-width="80" show-overflow-tooltip></el-table-column>
			<el-table-column inline-template :context="_self" label="" width="180" fixed="right">
	             <span>
					<el-button type="primary" size="small" @click="handleEchart(row)"></el-button>
				</span>
			</el-table-column>
		</el-table>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom:10px;">
			<el-pagination @current-change="handleCurrentChange" :page-size="10" layout="total, prev, pager, next, jumper" :total="total" style="float:right;"></el-pagination>
		</el-col>

		<ruleDialog></ruleDialog>
	</section>
</template>
<script>
	import ruleAnalysisApi from  '../../api/ruleAnalysis'
	import ruleDialog from './ruleAnalysis.dialog.vue'
	export default {
		components: {
			ruleDialog
		},
		data() {
			return {
				pickerOptions: {
					shortcuts: [{
						text: '',
						onClick(picker) {
							const end = new Date();
							const start = new Date();
							start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
							picker.$emit('pick', [start, end]);
						}
					}, {
						text: '',
						onClick(picker) {
							const end = new Date();
							const start = new Date();
							start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
							picker.$emit('pick', [start, end]);
						}
					}, {
						text: '',
						onClick(picker) {
							const end = new Date();
							const start = new Date();
							start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
							picker.$emit('pick', [start, end]);
						}
					}]
				},
				//
				summaryData:{
					"totalRuleNum":0,
					"redLineRuleNum":0,
					"transferRuleNum":0,
					"avgTransferRuleNum":0
				},
				summaryLoading:false,
				filters: {
					dataRange:'',
					ruleType:'',
					smallRuleType:''
				},
				tableData:[],
				total: 0,
				page: 1,
				listLoading: false,
				formatterData:''
			};
		},
		watch: {

		},
		methods: {
			handleDownload(){
				if(this.formatterData){
					var beginDate = this.formatterData.slice(0,10);
					var endDate = this.formatterData.substr(this.formatterData.length-10);
				}else{
					var beginDate = '';
					var endDate = '';
				}
				var params = {
					beginDate: beginDate,
					endDate:endDate,
					ruleType:this.filters.ruleType,
					smallRuleType:this.filters.smallRuleType
				};
				ruleAnalysisApi.export(params);
			},
			handleCurrentChange(val) {
				this.page = val;
				this.search();
			},
			search(){
				var _this = this;
				if(this.formatterData){
					var beginDate = this.formatterData.slice(0,10);
					var endDate = this.formatterData.substr(this.formatterData.length-10);
				}else{
					var beginDate = '';
					var endDate = '';
				}
				var params = {
					pageNum:_this.page, //
					pageSize:10,   //10
					beginDate: beginDate,
					endDate:endDate,
					ruleType:_this.filters.ruleType,
					smallRuleType:_this.filters.smallRuleType
				};
				_this.listLoading = true;
				ruleAnalysisApi.getList(params)
						.then(res => {
					_this.total = res.totalElements;
				for(var i = 0,len = res.content.length; i < len; i++){
					var _data = res.content[i].statisticsDate;
					if(_data && _data.length > 10){
						res.content[i].statisticsDate = _data.slice(0,10);
					}
				}
				_this.tableData = res.content;
				_this.listLoading = false;
			})
			.catch(function (error) {
					_this.listLoading = false;
					_this.$Error(_this,error);
				})
			},
			//
			getData(val){
				this.formatterData = val;
			},
			getSummary(){
				var _this = this;
				_this.summaryLoading = true;
				ruleAnalysisApi.getSummary({}).then(res =>{
					_this.summaryData = res;
				_this.summaryLoading = false;
			}).catch(error =>{
					_this.$Error(_this,error);
				_this.summaryLoading = false;
			});
			},
			format_statisticsDate(row,col){
				if(row.statisticsDate.length > 10){
					return row.statisticsDate.slice(0,10);
				}else{
					return row.statisticsDat;
				}
			},
			format_creditAddedAmount(row,col){
				return this.$Utils.addThousandSign(row.creditAddedAmount,2);
			},
			handleEchart(row){
				var data = {
					smallRuleTypeId:row.smallRuleTypeId,
					countWay:'3',
					date:row.statisticsDate
				};
				this.$bus.$emit('ruleAnalysisDialogShow','',data);
			}
		},
		mounted() {
			this.getSummary();
			this.search();
		}
	}
</script>
<style scoped>
	/*.el-date-editor--daterange.el-input{width: 100%;}*/
</style>


