<template>
	<el-dialog :title="editFormTtile" size="tiny" v-model="editFormVisible" @close="hideDialog">
		<el-form :model="editForm" label-width="90px" :rules="editFormRules" ref="editForm" id="editForm">
			<el-row>
				<el-col :span="24">
					<el-form-item label="" prop="resName" >
						<el-input v-model="editForm.resName" name="resName" auto-complete="off" placeholder=""></el-input>
					</el-form-item>
				</el-col>
			</el-row>

			<el-row>
				<el-col :span="24">
					<el-form-item label="" prop="resUrl">
						<el-input  v-model="editForm.resUrl" name="resUrl" auto-complete="off" placeholder=""></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="24">
					<el-form-item label="" prop="orderId">
						<el-input v-model="editForm.orderId" name="orderId" auto-complete="off" placeholder=""></el-input>
					</el-form-item>
				</el-col>

			</el-row>

			<el-row>
				<el-col :span="24">
					<el-form-item label="" prop="alias">
						<el-input type="textarea" v-model="editForm.alias" placeholder="" resize="none"></el-input>
					</el-form-item>
				</el-col>
			</el-row>

		</el-form>
		<div slot="footer" class="dialog-footer">
			<el-button @click.native="hideDialog"></el-button>
			<el-button type="primary" @click.native="Submit" :loading="editLoading">{{btnEditText}}</el-button>
		</div>
	</el-dialog>
</template>

<script>
    import assets from '../../../api/assets'
    export default {
        data() {
            var number = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else if(!this.$regExp.number.test(value)){
                    callback(new Error(''));
                } else {
                    callback();
                }
            };
            var reg = /[\u4e00-\u9fa5]/g;
            var urlRule =  (rule, value, callback) => {
                //debugger;
                if (value === '') {
                    callback(new Error(''));
                } else if(reg.test(value)){
                    callback(new Error(''));
                } else {
                    callback();
                }
            };
            return {
                editFormVisible: false,//
                editFormTtile: '',//
                //
                editForm: {
                    id: 0,
                    resName:'',
                    resUrl: '',
                    orderId:'',
                    alias:''
                },
                editLoading: false,
                btnEditText: '',
                SubmitType:'',
                editFormRules: { //
                    resName: [
                        { required: true, message: ''},
                        { max:20, message: '20'}
                    ],
                    resUrl: [
                        {required: true, validator: urlRule},
                        { max:20, message: '20'}
                    ],
                    orderId: [
                        {required: true, validator: number},
                        { max:20, message: '20'}
                    ],
                    alias:[
                        {required:false, max:150, message: '150'}
                    ]
                }
            };
        },
        created(){
            this.$bus.$off('menuDialogShow');
            this.$bus.$on('menuDialogShow', (title,data) => {
                this.menuDialogShow(title,data);
            })
        },
        methods: {
            hideDialog(){
                this.editFormVisible = false;
                setTimeout(() =>{
                    this.$refs.editForm.resetFields();
                },100)
            },
            //dialog
            menuDialogShow:function(title,data){
                var _this = this;
                _this.editFormTtile = title;
                if(data.id&&data.type=="lowerTree"){//
                    _this.SubmitType = "lowerTree";
                    this.editForm= {
                        id: data.id,
                        resName:'',
                        resUrl: '',
                        orderId:'',
                        alias:''
                    }
                }else if(data.id){//
                    _this.SubmitType = "edit";
                    _this.reload(data);
                }
                else{//
                    _this.SubmitType = "add";
                    this.editForm= {
                        id: 0,
                        resName:'',
                        resUrl: '',
                        orderId:'',
                        alias:''
                    }
                }
                _this.editFormVisible = true;
            },
            reload:function(data){
                this.editForm = data;
                if(this.editForm.orderId){
                    this.editForm.orderId = this.editForm.orderId.toString();
                }
            },
            // or 
            Submit: function () {
                var _this = this;
                _this.$refs.editForm.validate((valid) => {
                    if (valid) {
                        _this.editLoading = true;
                        _this.btnEditText = '';
                        if (_this.SubmitType == "add") {
                            //
                            var params = {
                                resName:_this.editForm.resName,
                                resUrl: _this.editForm.resUrl,
                                orderId:_this.editForm.orderId,
                                alias:_this.editForm.alias
                            };
                            assets.addTreeMenu(params).then((res) => {
                                _this.editLoading = false;
                            _this.btnEditText = '';
                            _this.editFormVisible = false;
                            _this.$message({
                                message: "",
                                type: 'success'
                            });
                            _this.$bus.$emit('getMenuList');
                        }).catch(function (error) {
                                _this.editLoading = false;
                                _this.btnEditText = '';
                                _this.$Error(_this,error);
                            });
                        } else if(_this.SubmitType == "lowerTree") {
                            //
                            var params = {
                                id: '',
                                resName:_this.editForm.resName,
                                resUrl: _this.editForm.resUrl,
                                orderId:_this.editForm.orderId,
                                alias:_this.editForm.alias,
                                access:'1',
                                superId:_this.editForm.id
                            };
                            assets.addTreeMenu(params).then((res) => {
                                _this.editLoading = false;
                            _this.btnEditText = '';
                            _this.editFormVisible = false;
                            _this.$message({
                                message: "",
                                type: 'success'
                            });
                            _this.$bus.$emit('getMenuList');
                        }).catch(function (error) {
                                _this.editFormVisible = false;
                                _this.editLoading = false;
                                _this.btnEditText = '';
                                _this.$Error(_this,error);
                            });
                        }else if(_this.SubmitType == "edit") {
                            //
                            var params = {
                                id: _this.editForm.id,
                                resName:_this.editForm.resName,
                                resUrl: _this.editForm.resUrl,
                                orderId:_this.editForm.orderId,
                                alias:_this.editForm.alias
                            };
                            if(params.alias =='null' || !params.alias){
                                params.alias = '';
                            }
                            assets.editTreeMenu(params).then((res) => {
                                _this.editLoading = false;
                            _this.btnEditText = '';
                            _this.editFormVisible = false;
                            _this.$message({
                                message: "",
                                type: 'success'
                            });
                            _this.$bus.$emit('getMenuList');
                        }).catch(function (error) {
                                _this.editFormVisible = false;
                                _this.editLoading = false;
                                _this.btnEditText = '';
                                _this.$Error(_this,error);
                            });
                        }
                       /* _this.$confirm('', '', {}).then(() => {

                        }).catch(() => {});*/
                    }
                });
            }
        },
        mounted() {
            var _this = this;
        }
    };
</script>




