<!---->
<template>
	<el-dialog :title="editFormTtile" v-model="editFormVisible" @close="hideDialog">
		<el-form :model="editForm" label-width="150px" :rules="editFormRules" ref="editForm" id="editForm">
			<el-row>
				<el-col :span="12">
					<el-form-item label="()" prop="resName">
						<el-input v-model="editForm.resName" placeholder="" auto-complete="off"></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="" prop="itemValue">
						<el-input v-model="editForm.itemValue" placeholder="" auto-complete="off"></el-input>
					</el-form-item>
				</el-col>
			</el-row>

			<el-row>
				<el-col :span="12">
					<el-form-item label="" prop="alias">
						<el-input  v-model="editForm.alias" placeholder="" auto-complete="off"></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="" prop="orderId">
						<!--<el-input-number v-model="editForm.orderId" :min="1" :max="200"></el-input-number>-->
						<el-input v-model="editForm.orderId" placeholder="" auto-complete="off"></el-input>
					</el-form-item>
				</el-col>
			</el-row>

			<el-row>
				<el-col :span="12">
					<el-form-item label="">
						<el-input  v-model="editForm.itemKey" placeholder=""></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="12">

					<el-form-item label="" prop="descp">
						<el-input type="textarea" v-model="editForm.descp" placeholder="" resize="none"></el-input>
					</el-form-item>
				</el-col>
			</el-row>

		</el-form>
		<div slot="footer" class="dialog-footer">
			<el-button @click.native="hideDialog"></el-button>
			<el-button type="primary" @click.native="editSubmit" :loading="editLoading">{{btnEditText}}</el-button>
		</div>
	</el-dialog>
</template>

<script>
	import dictionary from '../../../api/dictionary'
	export default {
		data() {
			var ValidatorNumber = (rule, value, callback) => {
				if (value === '') {
					callback(new Error(''));
				} else if(!this.$regExp.number.test(value)){
					callback(new Error(''));
				} else {
					callback();
				}
			};
			return {
				editFormVisible: false,//
				editFormTtile: '',//
				//
				editForm: {
					id: '',
					resName: '',
					itemValue: '',
					descp: '',
					alias: '',
					orderId: '',
					itemKey:''
				},
				editLoading: false,
				btnEditText: '',
				editFormRules: { //
					resName: [
						{ required: true, message: ''},
						{ max: 20, message: '20'}
					],
					itemValue: [
						{ required: true, message: ''},
						{ max: 20, message: '20'}
					],
					alias:[
						{ max: 20, message: '20',required:false}
					],
					orderId: [
						{required: true, validator: ValidatorNumber},
						{ max: 20, message: '20'}
					],
					descp: [
						{ required: true, message: ''},
						{ max: 150, message: '150'}
					]
				}
			};
		},
		created(){
            this.$bus.$off('dictionaryDialogShow');
			this.$bus.$on('dictionaryDialogShow', (title,data) => {
				//
				this.dictionaryDialogShow(title,data);
		})
		},
		methods: {
            hideDialog(){
                this.editFormVisible = false;
                setTimeout(() =>{
                    this.$refs['editForm'].resetFields();
                },100)
            },
			//dialog
			dictionaryDialogShow:function(title,data){
				this.editFormVisible = true;
				this.editFormTtile = title;
				if(data.id){
					this.reload(data);
				}else{
					this.editForm ={
						id: '',
						resName: '',
						itemValue: '',
						descp: '',
						alias: '',
						orderId: '',
						itemKey:'',
						parent_path:data.parent_path //
					}
				}
			},
			reload:function(data){
				this.editForm = data;
				if(this.editForm.orderId){
					this.editForm.orderId = this.editForm.orderId.toString();
				}
			},
			// or 
			editSubmit: function () {
				var _this = this;

				_this.$refs.editForm.validate((valid) => {
					if (valid) {
						_this.editLoading = true;
						_this.btnEditText = '';
						if (_this.editForm.id) {
							var msg = '';
						}else{
							var msg = '';
						}
						//
						dictionary.submit(_this.editForm).then((res) => {
							_this.editLoading = false;
						_this.btnEditText = '';
						this.$message({
							message: msg,
							type: 'success'
						});
						_this.editFormVisible = false;
						_this.$bus.$emit('reloadDictionaryList',_this.editForm.parent_path);
						}).catch(function (error) {
							_this.editLoading = false;
							_this.$Error(_this,error);
							_this.btnEditText = '';
						});


					}
				});

			}
		},
		mounted() {
			var _this = this;

		}
	};
</script>



