<!---->
<template>
	<div class="box">

		<el-form ref="form" :model="form"  :rules="rules"  label-width="150px">

			<el-form-item label="" style="font-weight: bold;">
				<el-col :span="12">
					<strong>{{productName}}</strong>
				</el-col>
			</el-form-item>

			<el-form-item label="" prop="maxCredit">
				<el-col :span="8">
					<el-input placeholder="" v-model="form.maxCredit" name="maxCredit" auto-complete="off" style="width: 210px;">
						<template slot="append"></template>
					</el-input>
				</el-col>
			</el-form-item>

			<el-form-item label="" prop="minCredit">
				<el-col :span="8">
					<el-input placeholder="" v-model="form.minCredit" name="minCredit" auto-complete="off" style="width: 210px;">
						<template slot="append"></template>
					</el-input>
				</el-col>
			</el-form-item>

			<el-form-item label=""  prop="enabledWhiteList">
				<el-switch on-text="" off-text=""  v-model="form.enabledWhiteList" @change="switchChange"></el-switch>
			</el-form-item>

			</el-form-item>


			<el-form-item label="" prop="expression">
				<el-col :span="8">
					<el-input placeholder="***" v-model="form.expression" style="min-width: 490px;">
						<el-button slot="append" icon="setting" @click="open_rule" ></el-button>
					</el-input>
				</el-col>
			</el-form-item>


			<el-form-item>
				<el-button  icon="d-arrow-left" @click="gotoBack"></el-button>
				<el-button type="primary" icon="check" @click="submitForm" :loading="subLoading"></el-button>
			</el-form-item>
		</el-form>


		<!---->
		<lineSetRuleDialog></lineSetRuleDialog>
	</div>
</template>


<script>
	import lineSetRuleDialog from './lineSet.ruleDialog' //
	import productList from '../../api/productList'
	export default {
		components: {
			lineSetRuleDialog
		},
		data() {
			var ValidatorNumber = (rule, value, callback) => {
				if (value === '') {
					callback(new Error(''));
				} else if(!this.$regExp.amountPlus.test(value)){
					callback(new Error(''));
				} else {
					callback();
				}
			};

			var ValidatorNumber2 = (rule, value, callback) => {
				if (value === '') {
					callback(new Error(''));
				} else if(!this.$regExp.amountPlus.test(value)){
					callback(new Error(''));
				} else {
					callback();
				}
			};

			return {
				id:'',
				productName:'',
				subLoading:false,
				form: {
					id: '',
					maxCredit: '',
					minCredit: '',
					enabledWhiteList: false,
					expression: ''
				},
				rules: {
					maxCredit: [
						{required: true, validator: ValidatorNumber},
						{max:10,message:'10'}
					],
					minCredit: [
						{required: true, validator: ValidatorNumber2},
						{max:10,message:'10'}
					],
					expression: [
						{  required: true, message: '' }
					],
					enabledWhiteList: [
						{ required: true, message: '' }
					]
				}
			}
		},
		created: function () {
			this.$bus.$on('lineSetRuleFn', (data) => {
				//
				this.lineSetRuleFn(data);
			})
		},
		mounted() {
			var query = this.$route.query;
			this.id = query.id;
			this.productName = query.name;
			this.getInfo();
		},
		methods:{
			getInfo(){
				var _this = this;
				productList.creditInfo_getById(_this.id).then(res =>{
					if(res.code =='-1'){
						_this.$message({
							type:'warning',
							messaage:res.msg || ''
						})
					}else{
						_this.form = res;
						if(_this.form.maxCredit){
							_this.form.maxCredit = _this.form.maxCredit.toString();
						}
						if(_this.form.minCredit){
							_this.form.minCredit = _this.form.minCredit.toString();
						}
						if(!res.maxCredit){
							setTimeout(function(){
								_this.$refs['form'].resetFields();
							},10)
						}

					}
				}).catch(error =>{
					_this.$Error(_this,error)
				});
			},
			//
			submitForm() {
				var _this = this;
				_this.$refs.form.validate((valid) => {
					if (valid) {
						var params = {
							id:_this.id, //id
							maxCredit:_this.form.maxCredit,
							minCredit:_this.form.minCredit,
							enabledWhiteList:_this.form.enabledWhiteList,
							expression:_this.form.expression
						}
						_this.subLoading = true;
						productList.creditInfo_put(params).then(res =>{
							//debugger;
							if(res.code =='0'){
								_this.$message({
									message:'',
									type:'success'
								})
							}else{
								_this.$message({
									message:res.msg ||'',
									type:'warning'
								})
							}
						  _this.subLoading = false;
						 _this.getInfo();
						}).catch(error =>{
							_this.$Error(_this,error);
						   _this.subLoading = false;
						});
					} else {

						return false;
					}
				});
			},
			//
			switchChange(){
				//alert(this.form.isUser)
				if(this.form.isUser){

				}
			},

			//
			gotoBack(){
				this.$router.push({ path: '/productList'});
			},
			//
			open_rule(){
				this.$bus.$emit('lineSetRuleDialog_open','',{productId:this.id});
			},
			//--
			lineSetRuleFn(data){
				var _this = this;
				if(data && typeof data =="object"){
					_this.form.rule = ''+data.ruleGrade+''+data.ruleNum+''+data.ruleType;
				}
			}
		}
	}
</script>

<style scoped>
	.h-title{margin: 10px auto 15px; font-size: 15px;}
</style>




