<!---->
<template>
	<el-dialog :title="editFormTtile" v-model="editFormVisible" @close="hideDialog" size="tiny">
		<el-form :model="editForm" label-width="85px" :rules="editFormRules" ref="editForm" id="editForm">
			<el-row>
				<el-col :span="24">
					<el-form-item label="" prop="name">
						<el-input v-model="editForm.name" auto-complete="off" placeholder=""></el-input>
					</el-form-item>
				</el-col>

			</el-row>

			<el-row>
				<el-col :span="24">
					<el-form-item label="" prop="descp">
						<el-input v-model="editForm.descp" type="textarea" auto-complete="off" re placeholder=""></el-input>
					</el-form-item>
				</el-col>
			</el-row>


			<el-row>
				<el-col :span="24">
					<el-form-item label="" prop="data">
						<el-input v-model="editForm.data" auto-complete="off" placeholder=""></el-input>
					</el-form-item>
				</el-col>
			</el-row>


		</el-form>
		<div slot="footer" class="dialog-footer">
			<el-button @click.native="hideDialog"> </el-button>
			<el-button type="primary" @click.native="editSubmit" :loading="editLoading">{{btnEditText}}</el-button>
		</div>
	</el-dialog>
</template>

<script>
	import dictionary from '../../../api/dictionary'
	import TreeVue  from './dictionary.tree'
	export default {
		data() {
			return {
				editFormVisible: false,//
				editFormTtile: '',//
				type:'',//CD
				//
				editForm: {
					'id':'',
					'name':'',
					'descp':'',
					'data':'',
					'parentPath':''
				},
				getData:{}, //
				editLoading: false,
				btnEditText: '',
				editFormRules: { //
					name: [
						{ required: true, message: ''},
						{ max:15, message: '15'}
					],
					descp: [
						{ required: true, message: ''},
			            { max:20, message: '20'}
					],
					data: [
						{ required: true, message: ''},
						{ max:20, message: '20'}
					]
				}
			};
		},
		created(){
            this.$bus.$off('treeDialogShow');
			this.$bus.$on('treeDialogShow', (title,data,type) => {
				this.treeDialogShow(title,data,type);
		    })
		},
		methods: {
            hideDialog(){
                this.editFormVisible = false;
				//this.resetTips();
				setTimeout(() =>{
				 this.$refs['editForm'].resetFields();
				 },100)
            },
			resetTips(){
				/*setTimeout(() =>{
					this.$refs['editForm'].resetFields();
				},100)*/
				this.$refs['editForm'].resetFields();
			},
			//dialog
			treeDialogShow:function(title,data,type){
				this.getData = data;
				//debugger;
				this.editFormVisible = true;
				this.editFormTtile = title;
				this.type = type;
				this.reload(data);
			},
			reload:function(data){
				this.editForm = data;
				//alert(JSON.stringify(this.editForm))

			},
			// or 
			editSubmit: function () {
				var _this = this;

				_this.$refs.editForm.validate((valid) => {
					if (valid) {
						//debugger;
						_this.editLoading = true;
						_this.btnEditText = '';
						if (_this.editForm.id) {
							if(_this.type=='addSub'){
								//
								var  para = {
									name: _this.editForm.name,
									descp: _this.editForm.descp,
									data: _this.editForm.data,
									//parentPath:_this.getData.fullPath,
									parentPath:_this.editForm.parentPath,
								};
								var msg =''
							}else if(_this.type=='edit'){
								//
								var para = {
									id: _this.editForm.id,
									name: _this.editForm.name,
									descp: _this.editForm.descp,
									data: _this.editForm.data
								};
								var msg =''
							}
							//debugger;
							dictionary.subTree(para).then((res) => {
								_this.editLoading = false;
							_this.btnEditText = '';
							_this.$message({
								message:msg ,
								type: 'success'
							});
							_this.editFormVisible = false;
							_this.$bus.$emit('dictionaryTreeReaload');
						}).catch (function(error){
								_this.editLoading = false;
								_this.btnEditText = '';
								_this.$Error(_this,error);
							});


						} else {
							// 

							let para = {
								name: _this.editForm.name,
								descp: _this.editForm.descp,
								parentPath:_this.editForm.parentPath,
								data:_this.editForm.data
							};
							dictionary.subTree(para).then((res) => {
							_this.editLoading = false;
							_this.btnEditText = '';
							_this.$message({
								message: '',
								type: 'success'
							});
							_this.editFormVisible = false;
							_this.$bus.$emit('dictionaryTreeReaload');
						}).catch (function(error){
								_this.editLoading = false;
								_this.btnEditText = '';
								_this.$Error(_this,error);
							});
						}
					}
				});

			}
		},
		mounted() {
			var _this = this;

		}
	};
</script>



