import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
import Login from '../pages/Login.vue'
import Home from '../pages/Home.vue'
import Main from '../pages/Main.vue'
import Box from '../pages/Box.vue'
import nofind from '../pages/404'

export default new Router({
    routes: [
        {
            path: '/',
            component: Home,
            name: '',
            meta:{index:0},//:
            show:true,//
            hidden: true,//
            children: [
                {
                    path: '/',
                    component: Box,
                    name: '',
                    children: [
                        { path: '/rule', component: require('../pages/ruleManager/rule.vue'), name: '', show:true },
                        { path: '/rule/edit', component: require('../pages/ruleManager/rule.edit.vue'), name: '', show:true },
                        { path: '/rule/add', component: require('../pages/ruleManager/rule.edit.vue'), name: '', show:true },
                        { path: '/rule/devSetting', component: require('../pages/ruleManager/rule.devSettings.vue'), name: '', show:true },
                        { path: '/rule/resSetting', component: require('../pages/ruleManager/rule.resSettings.vue'), name: '', show:true },
                        { path: '/ruleClass', component: require('../pages/ruleManager/ruleClass.vue'), name: '', show:true }
                    ],
                    button:[]
                },
                {
                    path: '/',
                    component: Box,
                    name: '',
                    children: [
                        { path: '/indicators', component: require('../pages/indicatorsManager/indicators.vue'), name: '', show:true },
                        { path: '/indicators/details', component: require('../pages/indicatorsManager/indicators.detail.vue'), name: '', show:false },
                        { path: '/indicators/edit', component: require('../pages/indicatorsManager/indicators.detail.edit.vue'), name: '', show:false },
                        { path: '/indicators/add', component: require('../pages/indicatorsManager/indicators.detail.edit.vue'), name: '', show:false },
                        { path: '/indicatorsClass', component: require('../pages/indicatorsManager/indicatorsClass.vue'), name: '', show:true }
                    ],
                    button:[]
                },
                {
                    path: '/',
                    component: Box,
                    name: '',
                    children: [
                        { path: '/productList',component: require('../pages/productManager/productList.vue'), name: '', show:true},
                        { path: '/productList/ruleSet', component: require('../pages/productManager/ruleSet.vue'), name: '', show:false},
                        { path: '/productList/ruleSet/edit', component: require('../pages/productManager/ruleSet.list.edit.vue'), name: '', show:false},
                        { path: '/productList/ruleSet/release', component: require('../pages/productManager/ruleSet.release.vue'), name: '', show:false},
                        { path: '/productList/scoreSet', component: require('../pages/productManager/scoreSet.vue'), name: '', show:false},
                        { path: '/productList/scoreSet/release', component: require('../pages/productManager/scoreSet.release.vue'), name: '', show:false},
                        { path: '/productList/scoreSet-result', component: require('../pages/productManager/scoreSet.result.vue'), name: '', show:false},
                        { path: '/productList/scoreSet-listView', component: require('../pages/productManager/scoreSet.list.view.vue'), name: '', show:false},
                        { path: '/productList/scoreSet-listView/edit', component: require('../pages/productManager/scoreSet.list.view.edit.vue'), name: '', show:false},
                        { path: '/productList/optionsResultSet', component: require('../pages/productManager/optionsResultSet.vue'), name: '', show:false},
                        { path: '/productList/optionsResultSet-add', component: require('../pages/productManager/optionsResultSet.addPage.vue'), name: '', show:false},
                        { path: '/productList/linesSet', component: require('../pages/productManager/lineSet.vue'), name: '', show:false},

                    ],
                    button:[]
                },
                {
                    path: '/',
                    component: Box,
                    name: '',
                    children: [
                        { path: '/members', component: require('../pages/nameManager/members.vue'), name: '', show:true},
                        { path: '/membersWhite', component: require('../pages/nameManager/membersWhite.vue'), name: '', show:true},
                        { path: '/membersBlack', component: require('../pages/nameManager/membersBlack.vue'), name: '', show:true}
                    ],
                    button:[]
                }
            ]

        },
        {
            path: '/',
            meta:{index:1},//:
            component: Home,
            name: '',
            show:true,//
            hidden: true,//
            children: [
                {
                    path: '/',
                    component: Box,
                    name: '',
                    children: [
                        { path: '/operator', component: require('../pages/systemManager/operatorManager/operator.vue'), name: '', show:true},
                        { path: '/role', component: require('../pages/systemManager/operatorManager/role.vue'), name: '', show:true},
                        { path: '/assets', component: require('../pages/systemManager/operatorManager/assets.vue'), name: '', show:true}
                    ],
                    button:[]
                },
                {
                    path: '/',
                    component: Box,
                    name: '',
                    children: [
                        { path: '/dictionary', component: require('../pages/systemManager/systemData/dictionary.vue'), name: '', show:true},
                        { path: '/settings', component: require('../pages/systemManager/systemData/settings.vue'), name: '', show:true}
                    ],
                    button:[]
                }

            ]

        },
        {
            path: '/',
            meta:{index:2},//:
            component: Home,
            name: '',
            show:true,//
            hidden: true,//
            children: [
                {
                    path: '/',
                    component: Box,
                    name: '',
                    children: [
                        { path: '/customerList', component: require('../pages/dataReports/customerList.vue'), name: '', show:true},
                        { path: '/simulate', component: require('../pages/dataReports/simulate.vue'), name: '', show:true},
                        { path: '/customerList/csReport', component: require('../pages/dataReports/reportView.vue'), name: '', show:false}
                    ],
                    button:[]
                }

            ]

        },
        {
            path: '/',
            meta:{index:3},//:
            component: Home,
            name: '',
            show:true,//
            hidden: true,//
            children: [
                {
                    path: '/',
                    component: Box,
                    name: '',
                    children: [
                        { path: '/riskSummary', component: require('../pages/riskReports/riskSummary.vue'), name: '', show:true},
                        { path: '/productAnalysis', component: require('../pages/riskReports/productAnalysis.vue'), name: '', show:true},
                        { path: '/ruleAnalysis', component: require('../pages/riskReports/ruleAnalysis.vue'), name: '', show:true},
                        { path: '/ruleAnalysis/demo', component: require('../pages/riskReports/ruleAnalysis.echarts.vue'), name: '', show:true},
                        { path: '/exceptionRule', component: require('../pages/riskReports/exceptionRule.vue'), name: '', show:true},
                        { path: '/scoreDistribution', component: require('../pages/riskReports/scoreDistribution.vue'), name: '', show:true},
                        { path: '/interfaceQuery', component: require('../pages/riskReports/interfaceQuery.vue'), name: '', show:true}
                    ],
                    button:[]
                }

            ]

        },
        {
            path: '/login',
            component: Login,
            name:'',
            show:false,//
            hidden: true//
        },
        {
            path: '/',
            component: Home,
            name: '',
            show:false,//
            hidden: true,//
            children: [
                { path: '/main', component: Main, name: '' }
            ]
        },
        {
            path: '/',
            component: Home,
            name: '',
            show:false,//
            hidden: true,//
            children: [
                { path: '/vueDev', component: require('../pages/systemManager/systemData/dictionary.tree'), name: 'vue' }
            ]
        },
        {
            path: '*',
            name: '404',
            component: nofind,
            show:false,//
            hidden: true//
        }
    ]
})



