<template>
	<section>
		<!---->
		<el-row>
			<el-form label-width="120px">
				<el-col :span="6">
					<el-form-item label="" :inline="true"> <el-input  placeholder="" v-model="filters.mobiles"></el-input></el-form-item>
				</el-col>
				<el-col :span="6">
					<el-form-item label="" :inline="true"> <el-input  placeholder="" v-model="filters.personName"></el-input></el-form-item>
				</el-col>
				<el-col :span="6">
					<el-form-item label="" :inline="true"> <el-input  placeholder="" v-model="filters.personIdCard"></el-input></el-form-item>
				</el-col>
				<el-col :span="6">
					<el-form-item class="btn_box">
						<el-button type="primary" icon="search" v-on:click="search()"></el-button>
					</el-form-item>
				</el-col>
			</el-form>
		</el-row>

        <!--table tool-->
        <el-row class="button_box">
            <el-button  icon="document" class="btn_border_none"  @click="handleDownload"></el-button>
            <el-button  icon="upload2" class="btn_border_none"  @click="handleImport"></el-button>
        </el-row>
		<el-table :data="tableData" border style="width: 100%" v-loading="listLoading">
			<el-table-column prop="personName" label="" width="100"></el-table-column>
			<el-table-column prop="mobiles" label="" width="127"></el-table-column>
			<el-table-column prop="personIdCard" label="" width="185"></el-table-column>
			<el-table-column prop="companyName" label="" min-width="150" show-overflow-tooltip></el-table-column>
			<el-table-column prop="isLeader" label="" width="80" :formatter="format_isLeader"></el-table-column>
			<el-table-column prop="maxLendNorm" label="" width="110" show-overflow-tooltip></el-table-column>
			<el-table-column prop="maxLoanTerm" label="" width="110" show-overflow-tooltip></el-table-column>
			<el-table-column prop="maxSingleLendNorm" label="" width="130" show-overflow-tooltip></el-table-column>
			<!--<el-table-column prop="availableCredit" label="" width="100" show-overflow-tooltip></el-table-column>-->
			<!--<el-table-column prop="usedCredit" label=""  width="100" show-overflow-tooltip></el-table-column>-->
			<!--<el-table-column prop="frozenCredit" label=""  width="100" show-overflow-tooltip></el-table-column>-->
			<el-table-column prop="effectiveTerm" label=""  width="180" show-overflow-tooltip></el-table-column>
			<el-table-column prop="status" label=""  width="80" :formatter="formatStatus"></el-table-column>
			<el-table-column inline-template :context="_self" label="" width="240" fixed="right">
	             <span>
					<el-button type="primary" size="small" @click="handleUserEdit(row)"></el-button>
					 <el-button type="success" size="small" @click="handleActiva(row)" v-if="row.status == 0"></el-button>
					 <el-button type="danger" size="small" @click="handleUnActiva(row)" v-else></el-button>
					 <el-button type="danger" size="small" @click="handleUserAddBlack(row)"></el-button>
				</span>
			</el-table-column>
		</el-table>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom:10px;">
			<el-pagination @current-change="handleCurrentChange" :page-size="10" layout="total, prev, pager, next, jumper" :total="total" style="float:right;"></el-pagination>
		</el-col>
        <!---->
		<user-dialog></user-dialog>
        <!---->
        <upload></upload>
	</section>
</template>

<script>
    import userDialog from './user.dialog.vue'
    import upload from './userWhite.upload.dialog.vue'
    import nameManager from '../../api/nameManager'
    export default {
        components: {
            userDialog,
            upload
        },
        data() {
            return {
                filters: {
                    mobiles: '',
                    personName:'',
                    personIdCard:''
                },
                tableData: [],
                total: 0,
                page: 1,
                listLoading: false,
            }
        },
        mounted(){
            this.search();
        },
        created: function () {
            this.$bus.$off('H_userWhite_reload');
            this.$bus.$on('H_userWhite_reload', () => {
                this.search();
        })
        },
        methods: {

            handleDownload(){
                window.location.href = '/dlapi/cms/archive/download?id=8256&fullPath=';
                return false;
            },

            handleImport(){
                this.$bus.$emit('H_userWhite_uploadShow',{title:''});
            },

            //
            search(val) {
                var _this = this;
                var params = {
                    pageNum:_this.page, //
                    pageSize:10,   //10
                    mobiles: this.filters.mobiles,
                    personIdCard: this.filters.personIdCard,
                    personName:this.filters.personName
                }
                _this.listLoading = true;
                nameManager.personwhite_get(params)
                        .then(res => {
                    //debugger;
                    _this.total = res.totalElements;
                _this.tableData = res.content;
                _this.listLoading = false;
            })
            .catch(function (error) {
                    _this.listLoading = false;
                    _this.$Error(_this,error);
                })
            },
            //formatter
            formatStatus(row,column){
                return row.status == 1 ? '' : row.status == 0 ? '' : '';
            },
            format_isLeader(row){
                return row.isLeader == 1 ? '' : '';
            },
            exportExel(){

            },

            handleCurrentChange(val) {
              this.page = val;
               this.search();
            },
            handleSelectionChange(val) {
//                this.multipleSelection = val;
            },
            handleUserEdit(row){
                var _data = this.$Utils.dataClone(row);
                this.$bus.$emit('H_userDialogShow','',_data);
            },
            handleActiva(row){
                var _this = this;
                _this.$confirm('', '', {
                }).then(() => {
                    nameManager.personwhite_disabled({ids:row.id,status:1})
                        .then(res => {
                    if(res.code=="0"){
                    _this.$message({
                        type: 'success',
                        message: ''
                    });
                }
                _this.search();

            })
            .catch(function (error) {
                    _this.$Error(_this,error);
                })
                })
			},
            handleUnActiva(row){
                var _this = this;
                _this.$confirm('', '', {
                }).then(() => {
                    nameManager.personwhite_disabled({ids:row.id,status:0})
                        .then(res => {
                    if(res.code=="0"){
                    _this.$message({
                        type: 'success',
                        message: ''
                    });
                }
                _this.search();

            })
            .catch(function (error) {
                    _this.$Error(_this,error);
                })
                })
            },
            handleUserAddBlack(row){
                var _this = this;
                _this.$confirm('', '', {
                }).then(() => {
                    nameManager.person_whiteToBlack_post({id:row.id}).then(res =>{
                    if(res.code =="0"){
                    _this.$message({
                        type: 'success',
                        message: ''
                    });
                    _this.search();
                }
            }).catch(error =>{
                    _this.$Error(_this,error)
            })

                    /*this.$message({
                        type: 'success',
                        message: ''
                    });*/
                })
            }
        }
    }
</script>

<style scoped>
	.el-form-item{ margin-bottom: 10px;}
</style>


