export default{
    install(Vue){
        Vue.prototype.$regExp={//
            kong: /\S/, //
            phone:/^1[34578]\d{9}$/,// 
            number:/^[0-9]*$/,//x
            numberd:/^\d+(\.\d+)?$/,//
            //numberdS:/^(\-|\+)?\d+(\.\d+)?$/,//
            numberdS:/^(\-)?\d+(\.\d+)?$/,//
            numberdOrAmount:/^\d+(\.\d{2})?$/,//
            amount : /^([+-]?)((\d{1,3}(,\d{3})*)|(\d+))(\.\d{2})?$/, //  ,,,.
            amountPlus:/^((\d{1,3}(,\d{3})*)|(\d+))(\.\d{2})?$/,  // ,amountReg
            date : /(\s(((0?[0-9])|([1-2][0-3]))\:([0-5]?[0-9])((\s)|(\:([0-5]?[0-9])))))?$/, // 
            area : /^[A-Za-z0-9\u4e00-\u9fa5]+$/, //10-200
            isZh:/^[\u4E00-\u9FA5]{2,4}$/, //
            contactReg : /^[A-Za-z\u4e00-\u9fa5]+$|[a-z]]/, // 
            contractNoReg : /\S/, //  26
            nameReg : /^[A-Za-z0-9\u4e00-\u9fa5]+$/, //  
            busReg:/^[A-Za-z0-9\u4e00-\u9fa5]+$/, // / 
            email:/^(\w)+(\.\w+)*@(\w)+((\.\w+)+)$/,//
            rate:/^((\d+\.?\d*)|(\d*\.\d+))\%$/,  //
            password:/^[\x21-\x7E]{6,20}$/,  //
            phoneEmail:/^[1]{1}[0-9]{10}$|(\w)+(\.\w+)*@(\w)+((\.\w+)+)$/,  //
            idCard:/(^\d{15}$)|(^\d{17}([0-9]|X)$)/, //
            banknum:/^(\d{16}|\d{18}|\d{19}|\d{20}|\d{21})$/  //
        };

        // Date Date String
        // (M)(d)(h)(m)(s)(q)  1-2 
        // (y) 1-4 (S) 1 ( 1-3 )
        // 
        // (new Date()).format("yyyy-MM-dd hh:mm:ss.S") ==> 2006-07-02 08:09:04.423
        // (new Date()).format("yyyy-M-d h:m:s.S")      ==> 2006-7-2 8:9:4.18
        Date.prototype.format = function (fmt) { //author: meizz
            var o = {
                "M+": this.getMonth() + 1,                 //
                "d+": this.getDate(),                    //
                "h+": this.getHours(),                   //
                "m+": this.getMinutes(),                 //
                "s+": this.getSeconds(),                 //
                "q+": Math.floor((this.getMonth() + 3) / 3), //
                "S": this.getMilliseconds()             //
            };
            if (/(y+)/.test(fmt))
                fmt = fmt.replace(RegExp.$1, (this.getFullYear() + '').substr(4 - RegExp.$1.length));
            for (var k in o)
                if (new RegExp('(' + k + ')').test(fmt))
                    fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)));
            return fmt;
        };

        Vue.prototype.$Utils={//
            /*
            *cookie
            *key  val   time
            * */
            setCookie: function(key,val,time){
                if(time){
                    var date=new Date(); //
                    var expiresDays=time;  //daten
                    date.setTime(date.getTime()+expiresDays*24*3600*1000); //cookie
                    document.cookie=key + "=" + val +";expires="+date.toGMTString();  //cookie
                }else{
                    document.cookie=key+"="+val;
                }
            },
            /*
            *cookie
             *key
             * */
            getCookie:function(key){
                var getCookie = document.cookie.replace(/[ ]/g,"");  //cookiecookie
                var arrCookie = getCookie.split(";");  //cookie"" cookiearrCookie
                var tips;  //tips
                for(var i=0;i<arrCookie.length;i++){   //forcookietips
                    var arr=arrCookie[i].split("=");   //cookie""cookiearr
                    if(key==arr[0]){  //arr[0]cookietips
                        tips=arr[1];   //cookietips
                        break;   //for
                    }
                }
                return tips;
            },
            /*
             *cookie
             *key
             * */
            deleteCookie:function(key){ //cookie
                var date = new Date(); //
                date.setTime(date.getTime()-10000); //date
                document.cookie = key + "=v; expires =" +date.toGMTString();//cookie
            },
            /*
             *
             *key
             * */
            dataClone:function(data){
                var _data = new Object();
                if(typeof data =='object'){
                    for( var k in data){
                        _data[k] = data[k];
                    }
                }
                return _data;
            },
            /*
            * 
            * key:string
            * */
            isNumber:function(val){
                var patrn = /^[0-9]*$/;
                if (patrn.exec(val) == null || val == "") {
                    return false
                } else {
                    return true
                }
            },
            /** 
             * @param num {Number} 
             * @param digits {Number} 
             **/
            addThousandSign(num, digits) {
                var num = parseFloat(num);
                num = '' + num.toFixed(digits);
                return num.replace(/(\d{1,3})(?=(\d{3})+(?:$|\.))/g, '$1,');
            },
            /** 
             * @param num {String} 
             **/
            delThousandSign(num){
                var x = num.split(',');
                return parseFloat(x.join(""));
            },
            /** 
             * @param str {String} 
             * @param num {Number} 
             **/
            getStrFirstPart(str, num){
                str = '' + str;
                num = Number(num);
                if(num > str.length){
                    num = str.length;
                }
                return str.slice(0, num);
            },

            /** 
             * @param str {String} 
             * @param num {Number} 
             **/
            getStrLastPart(str, num) {
                str = '' + str;
                num = Number(num);
                return str.slice(-num);
            },
            /** urlkey
             **/
            getUrlParam(name){
                var reg = new RegExp("(^|[&\?])" + name + "=([^&]*)(&|$)");
                var searchString = (location.hash) ? location.hash : window.location.search.substring(1);
                var r = searchString.match(reg);
                if (r != null)
                    return decodeURIComponent(r[2]);
            },
            /**url
             * **/
            getUrlParams(){
                var searchString = "";

                if (location.hash.indexOf("?") > 0) {

                    searchString = location.hash.slice(location.hash.indexOf("?") + 1);
                } else {

                    searchString = window.location.search.substring(1);
                }

                var params = searchString.split("&"), hash = {};

                if (searchString === "")
                    return {};
                for (var i = 0; i < params.length; i++) {
                    var val = params[i].split("=");
                    /* if(navigator.userAgent.indexOf("Safari") > -1){ //
                     hash[val[0]] = unescape(val[1]);
                     }else{
                     hash[unescape(val[0])] = decodeURIComponent(val[1]);
                     }*/
                    hash[unescape(val[0])] = decodeURIComponent(val[1]);

                }

                return hash;
            },
            //
            removeRepeatItem(arr){
                var newArr = [];
                for(var i=0;i<arr.length;i++){
                    if(newArr.indexOf(arr[i])===-1){
                        newArr.push(arr[i])
                    }
                }
                return newArr;
            },
            //id
            getAllFartherId(id,json){
                var AllCheckId = [id]; //
                var jsonOrg = json;  //
                getFartherId(id,json)
                function getFartherId(id,json){
                    for (var i=0;i<json.length;i++){
                        if(json[i].id==id){
                            AllCheckId.push(json[i].superId);
                            if(json[i].superId>0){
                                getFartherId(json[i].superId,jsonOrg)
                            }
                        }else if(json[i].children.length>0){
                            getFartherId(id,json[i].children)
                        }
                    }

                }
                return AllCheckId;
            },
            //checkedtrueID
            getAllCheckJsonId(json){
                var AllCheckId = [];
                function findCheckId(json){
                    for(var i = 0; i < json.length; i++){
                        if(json[i].checked){
                            AllCheckId.push(json[i].id);
                        }
                        if(json[i].children.length>0){
                            findCheckId(json[i].children);
                        }
                    }
                }
                findCheckId(json);
                return AllCheckId;
            }

        }
    }
}








