<!---->
<template>
    <el-dialog :title="editFormTtile" size="tiny" v-model="editFormVisible" :close-on-click-modal="false" @close="hideDialog()">
        <el-form :model="editForm" label-width="85px" :rules="editFormRules" ref="editForm" id="editForm">
            <el-row>
                <el-col :span="24">
                    <el-form-item label="" prop="name">
                        <el-input v-model="editForm.name" name="name" auto-complete="off"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="24">
                    <el-form-item label="" prop="extCode">
                        <el-input v-model="editForm.extCode" name="extCode" auto-complete="off"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="24">
                    <el-form-item label="" prop="enabled">
                        <el-radio-group v-model="editForm.enabled">
                            <el-radio label="1"></el-radio>
                            <el-radio label="0"></el-radio>
                        </el-radio-group>
                    </el-form-item>
                </el-col>
            </el-row>

        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click.native="editFormVisible = false"></el-button>
            <el-button type="primary" @click.native="editSubmit" :loading="editLoading">{{btnEditText}}</el-button>
        </div>
    </el-dialog>
</template>

<script>
    import productList from '../../api/productList'
    export default {
        data() {
            return {
                imageUrl:'',
                editFormVisible: false,//
                editFormTtile: '',//
                typePage:'',
                //
                editForm: {
                    id: '',
                    name: '',
                    extCode: '',
                    enabled:''
                },
                editLoading: false,
                btnEditText: '',

                editFormRules: { //
                    name: [
                        { required: true, message: '' },
                        { max:20, message: '20'}
                    ],
                    extCode: [
                        { required: true, message: ''},
                        { max:20, message: '20'}
                    ],
                    enabled: [
                        { required: true, message: ''}
                    ]

                }
            };
        },
        created(){
            this.$bus.$on('productListDialogShow', (title,data) => {
                //
                this.productListDialogShow(title,data);
        })
        },
        methods: {
            //dialog
            productListDialogShow:function(title,data){
                this.editFormVisible = true;
                this.editFormTtile = title;
                if(data.id){
                    this.reload(data);
                    this.typePage = 'edit';
                }else{
                    this.editForm = {
                        id: '',
                        name: '',
                        extCode: '',
                        enabled:''
                    }
                    this.typePage = 'add';
                }
            },
            hideDialog(){
                setTimeout(() =>{
                    this.$refs.editForm.resetFields();
            },100)
            },
            reload:function(data){
                this.editForm = data;
            },
            // or 
            editSubmit: function () {
                var _this = this;

                _this.$refs.editForm.validate((valid) => {
                    if (valid) {

                        _this.editLoading = true;
                        _this.btnEditText = '';
                        if (_this.editForm.id) {
                            var msg ="";
                        }else{
                            var msg ="";
                        }
                        var params = {
                            id: _this.editForm.id ||'',
                            name: _this.editForm.name,
                            extCode: _this.editForm.extCode,
                            enabled: _this.editForm.enabled =='1'?true:false
                        };
                        if(_this.typePage =='edit'){ //
                            productList.productList_put(params).then((res) => {
                                _this.editLoading = false;
                            _this.btnEditText = '';
                            _this.editFormVisible = false;
                            _this.$message({
                                message: msg,
                                type: 'success'
                            });
                            _this.$bus.$emit('H_productList_reload');
                        }).catch(function (error) {
                                _this.editLoading = false;
                                _this.btnEditText = '';
                                _this.$Error(_this,error);
                            });
                        }else{
                            productList.productList_post(params).then((res) => {
                                _this.editLoading = false;
                            _this.btnEditText = '';
                            _this.editFormVisible = false;
                            _this.$message({
                                message: msg,
                                type: 'success'
                            });
                            _this.$bus.$emit('H_productList_reload');
                        }).catch(function (error) {
                                _this.editLoading = false;
                                _this.btnEditText = '';
                                _this.$Error(_this,error);
                            });
                        }




                    }
                });

            }
        },
        mounted() {
            var _this = this;
        }
    };
</script>

<style scoped>
    .line{ text-align: center; font-size: 14px;}
</style>



