<!---->
<template>
	<el-dialog :title="editFormTtile" size="small" v-model="editFormVisible" :close-on-click-modal="false">
		<el-form :model="editForm" label-width="170px" :rules="editFormRules" ref="editForm" id="editForm">
			<el-row>
				<el-col :span="12">
					<el-form-item label="" prop="name">
						<el-input placeholder="" v-model="editForm.name" name="name" auto-complete="off"></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="" prop="name">
						<el-input v-model="editForm.name" name="name" auto-complete="off"></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="12">
					<el-form-item label="" prop="name">
						<el-input v-model="editForm.name" name="name" auto-complete="off"></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="" prop="name">
						<el-input placeholder="" v-model="editForm.name" name="name" auto-complete="off">
							<template slot="append"></template>
						</el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="12">
					<el-form-item label="" prop="name">
						<el-input placeholder="" v-model="editForm.name" name="name" auto-complete="off">
							<template slot="append"></template>
						</el-input>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="" prop="name">
						<el-input placeholder="" v-model="editForm.import" name="import" auto-complete="off">
							<template slot="append"></template>
						</el-input>
					</el-form-item>
				</el-col>
			</el-row>

			<el-row>
				<el-col :span="12">
					<el-form-item label="" prop="name">
						<el-input placeholder="" v-model="editForm.desc">
							<template slot="append"></template>
						</el-input>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="" prop="name">
						<el-input placeholder="" v-model="editForm.import" name="import" auto-complete="off"></el-input>
					</el-form-item>
				</el-col>
			</el-row>

			<el-row>
				<el-col :span="12">
					<el-form-item label="" prop="name">
						<el-date-picker v-model="value1" type="date" placeholder="" :picker-options="pickerOptions0" :editable="false"></el-date-picker>
					</el-form-item>
				</el-col>
			</el-row>


		</el-form>
		<div slot="footer" class="dialog-footer">
			<el-button @click.native="editFormVisible = false"></el-button>
			<el-button type="primary" @click.native="editSubmit" :loading="editLoading">{{btnEditText}}</el-button>
		</div>
	</el-dialog>
</template>

<script>
    export default {
        data() {
            return {
                value1: '',
                pickerOptions0: {
                    disabledDate(time) {
                        return time.getTime() < Date.now() - 8.64e7;
                    }
                },
                editFormVisible: false,//
                editFormTtile: '',//
                editFormInput:true,//input
                //
                editForm: {
                    id: 0,
                    name: '',
                    number: '',
                    regionBig:'',
                    regionSmall:'',
                    desc:'',
                    state:'',
                    import:''
                },
                editLoading: false,
                btnEditText: '',

                editFormRules: { //
                    name: [
                        { required: true, message: ''}
                    ],
                    number: [
                        { required: true, message: ''}
                    ],
                    regionBig: [
                        { required: true, message: ''}
                    ],
                    regionSmall: [
                        { required: true, message: ''}
                    ],
                    state: [
                        { required: true, message: ''}
                    ],
                    import:[
                        { required: true, message: ''}
					]
                }
            };
        },
        created(){
            this.$bus.$off('companyDialogShow');
            this.$bus.$on('companyDialogShow', (title,data) => {
                //
                this.DialogShow(title,data);
            })
        },
        methods: {
            //dialog
            DialogShow:function(title,data){
                this.editFormVisible = true;
                this.editFormTtile = title;
                if(data.id){
                    this.editFormInput=false;//
                    this.reload(data);
                }
            },
            reload:function(data){
                this.editForm = data;
            },
            // or 
            editSubmit: function () {
                var _this = this;

                _this.$refs.editForm.validate((valid) => {
                    if (valid) {
                        _this.editLoading = true;
                        _this.btnEditText = '';
                        if (_this.editForm.id == 0) {
                            //
                            var params = {
//                                    id:'',
//                                    photo:'',
                                name: _this.editForm.name,
                                realName: _this.editForm.realName,
                                password: _this.editForm.password,
                                confirmPassword:_this.editForm.confirmPassword,
                                mobile: _this.editForm.mobile,
                                email: _this.editForm.email,
                                workNumber: _this.editForm.workNumber
                            };
                            operator.add(params).then((res) => {
                                _this.editLoading = false;
                            _this.btnEditText = '';
                            _this.editFormVisible = false;
                            _this.$message({
                                message: "",
                                type: 'success'
                            });
                            _this.$bus.$emit('updatagOperator');
                        }).catch(function (error) {
                                _this.editLoading = false;
                                _this.btnEditText = '';
                                _this.$Error(_this,error);
                            });
                        } else {
                            //
                            var params = {
                                id: _this.editForm.id,
//                                    photo:'',
                                name: _this.editForm.name,
                                realName: _this.editForm.realName,
                                mobile: _this.editForm.mobile,
                                email: _this.editForm.email,
                                workNumber: _this.editForm.workNumber
                            };
                            operator.editInfo(params).then((res) => {
                                _this.editLoading = false;
                            _this.btnEditText = '';
                            _this.editFormVisible = false;
                            _this.$message({
                                message: "",
                                type: 'success'
                            });
                            _this.$bus.$emit('updatagOperator');
                        }).catch(function (error) {
                                _this.editLoading = false;
                                _this.btnEditText = '';
                                _this.$Error(_this,error);
                            });

                        }
                       /* _this.$confirm('', '', {}).then(() => {


                        }).catch(() => {});*/

                    }
                });

            }
        },
        mounted() {
            var _this = this;
        }
    };
</script>



