import user from '../api/user'
export default{
    install(Vue){
        Vue.prototype.$Error=function(obj,error){
            var _this = obj;
            if(typeof error.response !=="object") return;
            let state = error.response.status;
            if (state == 401) {//Token
                user.delInfo();
                _this.$alert('', '', {
                    confirmButtonText: '',
                    callback: action => {
                        _this.$router.replace('/login');
                    }
                });
            }else if (state == 403) {//
                _this.$message({
                    message: '',
                    type: 'error'
                });
            }
            else if (state == 400) {
                var errDate=JSON.parse(error.response.request.responseText);
                _this.$message({
                    message: errDate.msg,
                    type: 'error'
                });
            }else if(state == 415){
                _this.$message({
                    message: "",
                    type: 'error'
                });
            }
            else{
                _this.$message({
                    message: "",
                    type: 'error'
                });
            }
        }
    }
}












