<!---->
<template>
	<div>
		<el-dialog :title="editFormTtile" size="small" v-model="editFormVisible" @close="hideDialog">
			<el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm" id="editForm">
				<el-row>
					<el-col :span="12">
						<el-form-item label="" prop="name">
							<el-input v-model="editForm.name" name="name" placeholder="" auto-complete="off"></el-input>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="" prop="code">
							<el-input v-model="editForm.code" name="code" placeholder="" auto-complete="off" :disabled="inputdisabled"></el-input>
						</el-form-item>
					</el-col>
				</el-row>
				<el-row>
					<el-col :span="12">
						<el-form-item label="" required>
							<el-row>
								<el-col :span="11">
									<el-form-item prop="types">
										<el-select v-model="editForm.types" filterable placeholder="">
											<el-option v-for="item in options" :label="item.name" :value="item.id"></el-option>
										</el-select>
									</el-form-item>
								</el-col>
								<el-col class="line" :span="2">-</el-col>
								<el-col :span="11">
									<el-form-item prop="smallTypes">
										<el-select v-model="editForm.smallTypes" filterable placeholder="" >
											<el-option v-for="item in subsetoptions" :label="item.name" :value="item.id"></el-option>
										</el-select>
									</el-form-item>
								</el-col>
							</el-row>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="" prop="enabled">
							<el-radio-group v-model="editForm.enabled">
								<el-radio label="true"></el-radio>
								<el-radio label="false"></el-radio>
							</el-radio-group>
						</el-form-item>
					</el-col>

				</el-row>
				<el-row>
					<el-col :span="12">
						<el-form-item label="" prop="weight">
							<el-input v-model="editForm.weight"  placeholder=""></el-input>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="" prop="notes">
							<el-input type="textarea" v-model="editForm.notes" resize="none"  placeholder=""></el-input>
						</el-form-item>
					</el-col>
				</el-row>

				<el-row v-show="RuleSetting">
					<el-col :span="24">
						<el-form-item label="" prop="expression">
							<v-editor :input-content="inputContent" :upload-url="uploadUrl" v-model="editForm.expression" name="expression"></v-editor>
						</el-form-item>
					</el-col>
				</el-row>

			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="hideDialog"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading">{{btnEditText}}</el-button>
			</div>
		</el-dialog>

	</div>

</template>

<script>
    import { mapState } from 'vuex'
    import Editor from '../../componnets/wangEditor/Editor.vue'
    import rule from '../../api/ruleManager'
    import ruleConfig from '../../api/ruleConfig' //
    export default {
        components: {
            'v-editor': Editor,
        },
        data() {
            var ValidatorNumber = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else if(!this.$regExp.number.test(value)){
                    callback(new Error(''));
                } else {
                    callback();
                }
            };
            var ValidatorWeight = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else if(!this.$regExp.number.test(value)){
                    callback(new Error(''));
                } else {
                    callback();
                }
            };
            var ValidatorExpression = (rule, value, callback) => {
                if (value === ''|| value === '<p><br></p>') {
                    callback(new Error(''));
                } else {
                    callback();
                }
            };
            return {
                HShow:'', //
                type:'',//
                inputContent: '',  // 
                uploadUrl: '',  // 

                imageUrl:'',
                editFormVisible: false,//
                editFormTtile: '',//
				inputdisabled:false,//input

				sid:'',
				truesid:true,
                //
                editForm: {
                    id: 0,
                    name: '',
                    code: '',
                    expression:'',
                    types:'',
                    smallTypes:'',
                    weight:'',
                    notes:'',
                    enabled: '',// 
                },
                options: [],
                subsetoptions:[],
                editLoading: false,
                btnEditText: '',

                editFormRules: { //
                    name: [
                        { required: true, message: ''},
                        { max:20, message: '20'}
                    ],
                    code: [
                        {required: true, validator: ValidatorNumber},
                        { max:20, message: '20'}
                    ],
                    weight: [
                        {required: true, validator: ValidatorWeight},
                        { max:20, message: '20'}
                    ],
                    types: [
                        { required: true, message: ''}
                    ],
                    smallTypes: [
                        { required: true, message: ''}
                    ],
                    enabled: [
                        { required: true, message: '' }
                    ],
                    expression:[
                        {required: true, validator:ValidatorExpression}
                    ],
                    notes:[
                        { mim:0, max:200, message: '200'}
                    ]

                }
            };
        },
        watch: {
            'editForm.types'(curVal,oldVal){
                if(curVal==''){
                    return;
				}else{
                    this.editForm.smallTypes='';
                    this.getsmallTypesoptions(curVal);
				}
            },
            'editForm.expression'(curVal,oldVal){
                if(curVal=='<p><br></p>'){
                    return;
                }else{
                    var _this = this;
                    _this.editForm.expression=curVal;
                    setTimeout(() =>{
                        _this.$refs.editForm.validateField( 'expression');
                    },1)
				}

            }
        },
        created(){
            this.$bus.$off('ruleDialogShow');
            this.$bus.$on('ruleDialogShow', (title,data,type) => {
                this.inputContent='';
                this.type = type;
                this.ruleDialogShow(title,data);
            });
            //alert("created:"+this.RuleSetting);
            //this.HShow = this.RuleSetting;

        },
        methods: {
            hideDialog(){
                this.editFormVisible = false;
                setTimeout(() =>{
                    this.$refs['editForm'].resetFields();
                    this.inputContent='<p><br></p>';
                    this.editForm.expression='<p><br></p>';
                },100)
            },
            //dialog
            ruleDialogShow(title,data){
                // this.watchtypes = this.editForm.types;
                this.inputdisabled=false;
                this.editFormTtile = title;
                this.editFormVisible = true;
                if(data.id){
                    this.reload(data);
                    this.inputdisabled=true;
                }else{ //--YS
                    this.inputContent = ruleConfig.expression;
                    this.editForm.expression = ruleConfig.expression;
                }
            },
            reload(data){
                var _this = this;
                rule.getRuleInfo(data).then((res) => {
                    this.editForm = res;
					this.inputContent=res.expression;
				    this.truesid=true;
                    this.sid = res.smallTypes;
					this.editForm.types =parseInt(res.types);
					this.editForm.enabled =String(res.enabled);
				}).catch(function (error) {
                    _this.$Error(_this,error);
                });
            },
            getsmallTypesoptions(id){//
				//debugger;
                var _this = this;
                var params = {
                    type:'RULE',
                    parentId:id
                }
                rule.getoTypesptions(params)
					.then(res=>{
						if(res.code=="-1"){
							_this.$notify({
								title: '',
								message: res.msg,
								type: 'error'
							});
						}else{
							_this.subsetoptions = res;
						}
						if(_this.sid&&_this.truesid){
                            _this.truesid=false;
							_this.editForm.smallTypes =parseInt(_this.sid);
						}
					})
					.catch(function (error) {
					    _this.$Error(_this,error);
				    })
            },
            getoTypesptions(){//
                var params = {
                    type:'RULE',
                    parentId:'-1'
                }
                rule.getoTypesptions(params).then(res=>{
                    if(res.code=="-1"){
						this.$notify({
							title: '',
							message: res.msg,
							type: 'error'
						});
					}else{
						this.options = res;
					}

                }).catch(function (error) {
                    this.$Error(this,error);
                })
            },
            // or 
            editSubmit: function () {
                var _this = this;

                _this.$refs.editForm.validate((valid) => {
                    if (valid) {
                        _this.editLoading = true;
                        _this.btnEditText = '';
                        if (_this.type == "add") {
                            //
                            var params = {
                                name: _this.editForm.name,
                                code: _this.editForm.code,
                                expression:_this.editForm.expression,
                                types:_this.editForm.types,
                                smallTypes:_this.editForm.smallTypes,
                                weight:_this.editForm.weight,
                                notes:_this.editForm.notes,
                                enabled: _this.editForm.enabled
                            };
                            rule.addRule(params).then((res) => {
                                _this.editLoading = false;
                            _this.btnEditText = '';
                            _this.editFormVisible = false;
                            _this.$message({
                                message: "",
                                type: 'success'
                            });
                            _this.$bus.$emit('updataRuleList');
                        }).catch(function (error) {
                                _this.editLoading = false;
                                _this.btnEditText = '';
                                _this.$Error(_this,error);
                            });
                        } else if(_this.type == "edit") {
                            //
                            var params = {
                                id: _this.editForm.id,
                                name: _this.editForm.name,
                                code: _this.editForm.code,
                                expression:_this.editForm.expression,
                                types:_this.editForm.types,
                                smallTypes:_this.editForm.smallTypes,
                                weight:_this.editForm.weight,
                                notes:_this.editForm.notes,
                                enabled: _this.editForm.enabled
                            };
                            rule.editRule(params).then((res) => {
                                _this.editLoading = false;
                            _this.btnEditText = '';
                            _this.editFormVisible = false;
                            _this.$message({
                                message: "",
                                type: 'success'
                            });
                            _this.$bus.$emit('updataRuleList');
                        }).catch(function (error) {
                                _this.editLoading = false;
                                _this.btnEditText = '';
                                _this.$Error(_this,error);
                            });

                        }

                        /*_this.$confirm('', '', {}).then(() => {


                        }).catch(() => {});*/



                    }
                });
            }
        },
        mounted() {
            this.getoTypesptions();
        },
        computed: {
                ...mapState([
                    'RuleSetting'
                ])
        },
    };
</script>
<style scoped>
	.line{ text-align: center; font-size: 14px;}
</style>



