import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

// 
const state = {
    ProjectName:'DeeLon',
    RuleSetting: false, //
}

//  mutations
const mutations = {

    // 
    UPDATE_PROJECTNAME(state, name) {
        state.ProjectName = name;
    },
    // 
    UPDATE_RULESETTING(state, val) {
        state.RuleSetting = val;
        //console.log('store...:'+state.RuleSetting);
    }

}

//  store 
export default new Vuex.Store({
    state,
    mutations
})



