<template>
	<div class="box">
		<el-form ref="form" :model="form"  :rules="rules"  label-width="130px">

			<el-form-item label="" style="font-weight: bold;">
				<el-col :span="8">
					<strong>{{form.productName}}</strong>
				</el-col>
			</el-form-item>

			<el-form-item label="" prop="name">
				<el-col :span="8">
					<el-input v-model="form.name" placeholder=""></el-input>
				</el-col>
			</el-form-item>

			<el-form-item label="" prop="ruleResult">
				<el-col :span="8">
					<el-input v-model="form.ruleResult" placeholder="" :disabled="true"></el-input>
				</el-col>
				<el-col :span="2" style="margin-left: 10px;">
					<el-button type="info" icon="setting" @click="open_rule"></el-button>
				</el-col>
			</el-form-item>

			<el-form-item label="" prop="gradeResult">
				<el-col :span="8">
					<el-input v-model="form.gradeResult" placeholder="" :disabled="true"></el-input>
				</el-col>
				<el-col :span="2" style="margin-left: 10px;">
					<el-button type="info" icon="setting" @click="open_result"></el-button>
				</el-col>
			</el-form-item>

			<el-form-item label="" prop="result">
				<el-col :span="8">
					<el-input v-model="form.result" name="result"  placeholder="" :disabled="true"></el-input>
				</el-col>
				<el-col :span="2" style="margin-left: 10px;">
					<el-button type="info" icon="setting" @click="open_options"></el-button>
				</el-col>
			</el-form-item>





			<el-form-item label="" prop="remark">
				<el-col :span="8">
					<el-input type="textarea" resize="none" v-model="form.remark" placeholder=""></el-input>
				</el-col>

			</el-form-item>

			<el-form-item>
				<el-button  icon="d-arrow-left" @click="gotoBack"></el-button>
				<el-button type="primary" icon="check" @click="submit" :loading="editLoading">{{submitTest}}</el-button>
			</el-form-item>
		</el-form>

		<!---->
		<optionsResultSetPageDialog></optionsResultSetPageDialog>

		<!---->
		<optionsResultSetPageRuleDialog></optionsResultSetPageRuleDialog>
	</div>
</template>


<script>
	import optionsResultSetPageDialog from './optionsResultSet.addPage.dialog.vue'  //  
	import optionsResultSetPageRuleDialog from './optionsResultSet.addPage.ruleDialog.vue' //    
	import productList from '../../api/productList' //API
	export default {
		components: {
			optionsResultSetPageDialog,
			optionsResultSetPageRuleDialog
		},
		data() {
			return {
				id:'',
				editLoading:false,
				submitTest:'',
				acType:'',
				productId:'',
				form: {
					"id":"",
					"productId":"",
					"productName":"",
					"name":"",
					"result":"",
					"gradeResult":"",
					"ruleResult":"",
					"remark":""
				},
				rules: {
					result: [
						{ required: true, message: ''}
					],
					name: [
						{ required: true, message: '' },
						{ max:20, message: '20'}
					],
					gradeResult: [
						{  required: true, message: ''}
					],
					ruleResult: [
						{ required: true, message: ''}
					],
					remark: [
						{ required: true, message: '' },
						{ max:150, message: '150'}
					]
				}
			}
		},
		methods:{

			gotoBack(){
				this.$router.push({ path: '/productList/optionsResultSet',query:{id:this.$route.query.productId,name:this.form.productName}});
			},

			// --
			open_options(){
				this.$bus.$emit('optionsResultSet_addPage_open','',{type:'result',productId:this.form.productId},{ labelName:'',
					labelNotes:''});
			},

			// --
			open_result(){
				this.$bus.$emit('optionsResultSet_addPage_open','',{type:'gradeResult',productId:this.form.productId,ruleResult:this.form.ruleResult});
			},

			//
			open_rule(){
				this.$bus.$emit('optionsResultSet_addPage_rule','',{type:'ruleResult',productId:this.id,gradeResult:this.form.gradeResult});
			},
			bindData(key,val){
				//debugger;
				this.form[key] = val;
			},
			//
			getInfo(){
				var _this = this;
				productList.decisionInfo_getById(_this.id).then(res =>{
					_this.form = res|| _this.form;
			}).catch(error =>{
					_this.$Error(_this,error);
			});
			},
			//
			submit(){
				var _this = this;
				_this.$refs.form.validate((valid) => {
					if(valid){
						_this.submitTest = '';
						_this.editLoading = true;
						//debugger;
						if(_this.acType=='Add'){
							var params = {
								productId:_this.productId,
								productName:_this.form.productName,
								name:_this.form.name,
								result:_this.form.result,
								gradeResult:_this.form.gradeResult,
								ruleResult:_this.form.ruleResult,
								remark:_this.form.remark
							};
							productList.decision_post(params).then(res =>{
								_this.submitTest='';
							_this.editLoading = false;
							_this.$message({
								type: 'success',
								message: ''
							});
							setTimeout(() =>{
								_this.gotoBack();
							},1000)
						}).catch(error =>{
								_this.$Error(_this,error);
							_this.submitTest='';
							_this.editLoading = false;
						});
						}else{
							productList.decision_put(_this.form).then(res =>{
								_this.submitTest='';
							_this.editLoading = false;
							_this.$message({
								type: 'success',
								message: ''
							});
							setTimeout(() =>{
								_this.gotoBack();
						},1000)
						}).catch(error =>{
								_this.$Error(_this,error);
							_this.submitTest='';
							_this.editLoading = false;
						});
						}

					}
				});

			},
		},
		created: function () {
			var _this = this;
			_this.$bus.$off('H_optionsResultSet_bindData');
			_this.$bus.$on('H_optionsResultSet_bindData', (type,name) => {
				//debugger;
				_this.bindData(type,name);
		   });

			_this.$bus.$on('H_optionsResultSet_bindData2', (ruleResult) => {
				//
				//this.bindData(key,val);
				//alert(val)
				alert(ruleResult)
				//_this.form.ruleResult = ruleResult;
			})
		},
		mounted() {
			this.id = this.$route.query.id;
			this.productId = this.$route.query.productId;
			this.form.productName ='';
			if(this.$route.query.aciton =='Add'){  //
				this.form.productName = this.$route.query.productName || '';
				this.acType = 'Add';
			}else{
				this.acType = 'Edit';
				this.getInfo();
			}


		}
	}
</script>

<style scoped>
	.h-title{margin: 10px auto 15px; font-size: 15px;}
</style>


