import axios from 'axios';
export default {
    getSetting:function(resName){ //-
        var params = {
            page:1,
            rows:10,
            pageNum:1,
            pageNumber:1,
            pageSize:10,
            column:'',
            condition:'',
            queryParams:JSON.stringify({
                resName: resName || '',
                alias:'',
                descp:'',
                parent_path:'////'
            })
        };
        return axios.get('/dlapi/cms/sysDic/search',{params: params}).then(res => res.data);
    }

};


