import axios from 'axios';

var getCookie=function(key){
    var getCookie = document.cookie.replace(/[ ]/g,"");  //cookiecookie
    var arrCookie = getCookie.split(";");  //cookie"" cookiearrCookie
    var tips;  //tips
    for(var i=0;i<arrCookie.length;i++){   //forcookietips
        var arr=arrCookie[i].split("=");   //cookie""cookiearr
        if(key==arr[0]){  //arr[0]cookietips
            tips=arr[1];   //cookietips
            break;   //for
        }
    }
    return tips;
};

var ajaxToken = function(){
    var token = getCookie("UserLoginToken");
    if(token){
        axios.defaults.headers.common['Token'] = token;   // Token
    }
    // var user = JSON.parse(sessionStorage.getItem("user")); //Token
    // if(user){
    //     axios.defaults.headers.common['Token'] = user.token;   // Token
    // }
}

