<template>
	<el-dialog :title="editFormTtile" size="tiny" v-model="editFormVisible" @close="hideDialog">
		<el-form :model="editForm" label-width="60px" :rules="editFormRules" ref="editForm">
			<el-row>
				<el-col :span="24">
					<el-form-item label="" prop="code" >
						<el-input v-model="editForm.code" name="code" auto-complete="off" placeholder=""></el-input>
					</el-form-item>
				</el-col>
			</el-row>

			<el-row>
				<el-col :span="24">
					<el-form-item label="" prop="name">
						<el-input  v-model="editForm.name" name="name" auto-complete="off" placeholder=""></el-input>
					</el-form-item>
				</el-col>

			</el-row>

			<el-row>
				<el-col :span="24">
					<el-form-item label="" prop="remark">
						<el-input type="textarea" v-model="editForm.remark" placeholder="" resize="none"></el-input>
					</el-form-item>
				</el-col>
			</el-row>

		</el-form>
		<div slot="footer" class="dialog-footer">
			<el-button @click.native="hideDialog"></el-button>
			<el-button type="primary" @click.native="editSubmit" :loading="editLoading">{{btnEditText}}</el-button>
		</div>
	</el-dialog>
</template>

<script>
    import role from '../../../api/role'
    export default {
        data() {
            return {
                type:'',
                editFormVisible: false,//
                editFormTtile: '',//
                //
                editForm: {
                    id: 0,
                    code:'',
                    name: '',
                    remark:''
                },
                editLoading: false,
                btnEditText: '',
                editFormRules: { //
                    code: [
                        { required: true, message: ''}
                    ],
                    name: [
                        { required: true, message: ''},
                        { min: 3, max: 20, message: ' 3  20 '}
                    ]
                }
            };
        },
        created(){
            this.$bus.$on('DialogShow', (title,data,type) => {
                this.type = type;
                this.roleDialogShow(title,data);
            })
        },
        methods: {
            hideDialog(){
                this.editFormVisible = false;
                setTimeout(() =>{
                    this.$refs.editForm.resetFields();
                },100)
            },
            //dialog
            roleDialogShow:function(title,data){
                var _this = this;
                _this.editFormTtile = title;
                if(data.id){//
                    _this.reload(data);
                    //_this.resetForm();//
                }
                else{//
                    this.editForm= {
                        id: 0,
                        code:'',
                        name: '',
                        remark:''
                    }

                }
                _this.editFormVisible = true;
            },
            resetForm(formName) {
                this.$refs['editForm'].resetFields();
            },
            reload:function(data){
                this.editForm = data;
            },
            // or 
            editSubmit: function () {
                var _this = this;

                _this.$refs.editForm.validate((valid) => {
                    if (valid) {
                        _this.editLoading = true;
                        _this.btnEditText = '';
                        if (_this.type== 'add') {
                            //
                            var params = {
                                code: _this.editForm.code,
                                name: _this.editForm.name,
                                remark: _this.editForm.remark
                            };
                            role.addrole(params).then((res) => {
                                _this.editLoading = false;
                            _this.btnEditText = '';
                            _this.editFormVisible = false;
                            _this.$message({
                                message: "",
                                type: 'success'
                            });
                            _this.$bus.$emit('updatagrole');
                        }).catch(function (error) {
                                _this.editLoading = false;
                                _this.btnEditText = '';
                                _this.$Error(_this,error);
                            });
                        } else {
                            //
                            var params = {
                                id: _this.editForm.id,
                                code: _this.editForm.code,
                                name: _this.editForm.name,
                                remark: _this.editForm.remark
                            };
                            role.addrole(params).then((res) => {
                                _this.editLoading = false;
                            _this.btnEditText = '';
                            _this.editFormVisible = false;
                            _this.$message({
                                message: "",
                                type: 'success'
                            });
                            _this.$bus.$emit('updatagrole');
                        }).catch(function (error) {
                                _this.editLoading = false;
                                _this.btnEditText = '';
                                _this.$Error(_this,error);
                            });
                        }
                       /* _this.$confirm('', '', {}).then(() => {

                        }).catch(() => {});*/
                    }
                });
            }
        },
        mounted() {
            var _this = this;
        }
    };
</script>




