<script>
	import riskReportApi from '../../api/riskReport'
	import reportView from './simulate.report'
	export default {
		components:{
			reportView
		},
		data() {

			//tab1
			var checkAge = (rule, value, callback) => {
				//debugger;
				if (value) {
					if (this.$regExp.number.test(value)) {
						//callback();
						if (value < 18) {
							this.errorActive = 'first';
							callback(new Error('18'));
						}else if(value > 100){
							this.errorActive = 'first';
							callback(new Error('100'));
						} else {
							callback();
						}
					}else{
						this.errorActive = 'first';
						callback(new Error(''));
					}
				}else{
					callback();
				}
			};
			var checkDeadline  = (rule, value, callback) => {
				if (value) {
					if (this.$regExp.number.test(value)) {
						callback();
					}else{
						this.errorActive = 'first';
						callback(new Error(''));
					}
				}else{
					this.errorActive = 'first';
					callback(new Error(''));
				}
			};



			var checkIdCard = (rule,value,callback) =>{
				if(value){
					if(this.$regExp.idCard.test(value)){
						callback();
					}else{
						this.errorActive = 'first';
						callback(new Error(''));
					}
				}else{
					this.errorActive = 'first';
					callback(new Error(''));
				}
			};
			var checkRealName = (rule,value,callback) =>{
				if(value){
					if(this.$regExp.contactReg.test(value)){
						callback();
					}else{
						this.errorActive = 'first';
						callback(new Error(''));
					}
				}else{
					this.errorActive = 'first';
					callback(new Error(''));
				}
			};
			var checkMobile = (rule,value,callback) =>{
				if(value){
					if(this.$regExp.phone.test(value)){
						callback();
					}else{
						this.errorActive = 'first';
						callback(new Error(''));
					}
				}else{
					this.errorActive = 'first';
					callback(new Error(''));
				}
			};

			var checkBankCard = (rule,value,callback) =>{
				if(value){
					if(this.$regExp.banknum.test(value)){
						callback();
					}else{
						this.errorActive = 'first';
						callback(new Error(''));
					}
				}else{
					this.errorActive = 'first';
					callback(new Error(''));
				}
			};


			//tab3
			var checkMunber  = (rule, value, callback) => {
				if (value) {
					if (this.$regExp.number.test(value)) {
						callback();
					}else{
						this.errorActive = 'third';
						callback(new Error(''));
					}
				}else{
					callback();
				}
			};

			return {
				activeName: 'first', //tab
				errorActive:'first',
				//
				value4:'',
				value5:'',
				//
				reportStatus:true,
				subLoading:false,
				textBtn:'',
				ruleForm: {
					//
					radio1:'',
					radio2:'',
					radio3:'',
					radio4:'',
					radio5:'1',
					radio6:'1',
					radio7:'',
					radio8:'',
					radio9:'',
					radio10:'',
					select1:'',
					select2:'',
					select3:'',
					select4:'',
					select5:'',
					select6:'',
					select7:'',
					select8:'',
					select9:'',
					select10:'',
					input1:'', //
					input2:'',
					input3:'',
					input4:'',
					input5:'',
					input7:'',
					input8:'',
					input9:'',
					input10:'',
					input11:'',

					//
					realName:'',
					mobile:'',
					bankCard:'',
					idCard: '',
					productCode: '',
					deadline: '',
					age: '',
					residenceTime: '',
					education: '',
					married: '',
					health: '',
					occupationOrUnit: '',
					positionOrTitle: '',
					postWorkingLife: '',
					industryDevelopmentStatus: '',
					personalMonthlyIncome: '',
					spouseMonthlyAverageIncome: '',
					debtToIncomeRatio: '',
					realEstateSituation: '',
					financialAssets: '',
					vehicleCondition: '',
					creditCardUsage: '',
					bankAccountInformation: '',
					faceRecognitionStatus: '',
					workingTime: '',
					workingStatus: '',
					overdueAmount: '',
					overdueHistory: ''
				},
				rules: {
					realName:[
						{ required: true, validator: checkRealName }
					],
					mobile:[
						{ required: true, validator: checkMobile }
					],
					bankCard:[
						{ required: true, validator: checkBankCard }
					],
					idCard:[
						{ required: true, validator: checkIdCard }
					],
					productCode:[
						{ required: true, message: '' },
						{ min: 3, max: 20, message: ' 3  20 '}
					],
					deadline:[
						{ required: true, validator: checkDeadline }
					],
					age: [
						{ required: false, validator: checkAge}
					],
					region: [
						{ required: true, message: ''}
					],
					//
					personalMonthlyIncome:[
						{required:false,validator:checkMunber}
					],
					spouseMonthlyAverageIncome:[
						{required:false,validator:checkMunber}
					],
					input1:[
						{required:false,validator:checkMunber}
					],
				}
			};
		},
		methods: {
			handleClick(tab, event) { //tab

			},
			show(){
				this.reportStatus = true;
			},
			hide(){
				this.reportStatus = false;
			},
			test(){
				var _this = this;
				//debugger;
				_this.hide();
				var result ={
					"applyNum": 1484728015391,
					"point": 11,
					"triggerLoggers":
							[
								"'25~35'+3 ",
								"''+2 ",
								"'5000~8000'+6 ",
								"6"
							],
					"suggestAmount": 2100,
					"level": "E",
					"decision": "SD",
					"ruleDecision": "RD",
					"overallDecision": "D"
				}
				setTimeout(function(){
					_this.$bus.$emit('H_simulate_echarts',{point:result.point||'',level:result.level||''});  ////
					_this.$bus.$emit('H_simulate_topData',result);
				},800)
			},
			submitForm(formName) {
				var _this = this;
				this.$refs[formName].validate((valid) => {
					if (valid) {
						_this.subLoading = true;
						_this.textBtn = '';
						riskReportApi.simulate(_this.ruleForm).then(res =>{

							if(res.code=='0'){
							//alert(res.result.userType)
							//debugger;
							if(res.result.userType =='NORMAL'){
								_this.hide();
								setTimeout(function(){
									_this.$bus.$emit('H_simulate_echarts',{point:res.result.point||'',level:res.result.level||''});  //
									_this.$bus.$emit('H_simulate_topData',res.result); //

									//
									_this.$bus.$emit('H_simulate_customer',{
										realName:res.result.realName,       //
										mobile:res.result.mobile,     //
										bankCard:res.result.bankCard,  //
										idCard:res.result.idCard,    //
										productCode:res.result.productCode,  //
										deadline:12

									});
									_this.$bus.$emit('H_simulate_idVerification',res.result.certification); //
									_this.$bus.$emit('H_simulate_preLoan',res.result.summaryList); //
									_this.$bus.$emit('H_simulate_hitRule',res.result.hitRule); //
									_this.$bus.$emit('H_simulate_hitRating',res.result.hitFact); //
								},800)
							}else{
								var typeName = res.result.userType == 'BLACK'? '':'';
								//var msg = ''+typeName+''+this.$Utils.toThousands(res.result.suggestAmount) +''
								var msg = ''+typeName+''+res.result.suggestAmount+''
								_this.$alert(msg, '', {
									confirmButtonText: ''
								});
							}

							}else{
								_this.$message.error(res.msg || '');
							}
						_this.subLoading = false;
						_this.textBtn = '';
						}).catch(error =>{
							_this.$Error(_this,error);
						_this.subLoading = false;
						_this.textBtn = '';
						});

					} else {

				_this.activeName=_this.errorActive;
				//_this.$message.warning('');
					return false;
				}
				});
			},
			resetForm(formName) {
				this.$refs[formName].resetFields();
				this.ruleForm = {
							realName:'',
							mobile:'',
							bankCard:'',
							idCard: '',
							productCode: '',
							deadline: 12,
							age: '',
							residenceTime: '',
							education: '',
							married: '',
							health: '',
							occupationOrUnit: '',
							positionOrTitle: '',
							postWorkingLife: '',
							industryDevelopmentStatus: '',
							personalMonthlyIncome: '',
							spouseMonthlyAverageIncome: '',
							debtToIncomeRatio: '',
							realEstateSituation: '',
							financialAssets: '',
							vehicleCondition: '',
							creditCardUsage: '',
							bankAccountInformation: '',
							faceRecognitionStatus: '',
							workingTime: '',
							workingStatus: '',
							overdueAmount: '',
							overdueHistory: ''
				}
			}
		},
		created(){
			this.$bus.$off('H_simulateShow');
			this.$bus.$on('H_simulateShow',()=>{
				this.show();
			});
		},
		mounted(){}
	}
</script>

<style scoped>
	#ruleForm{width: 100%; position: relative;}
	.h-fwidth{width: 100%; max-width: 320px;}
	.h-title{font-size: 14px;margin: 0; padding-left: 21px; height:40px; line-height: 40px; margin-bottom: 10px;}
	.h-tabMT{margin-bottom: 20px;}
	#h-tabBox input[type='text']{max-width: 250px!important;}
</style>


