
<template>
	<el-dialog :title="editFormTtile" size="tiny" v-model="editFormVisible"  @close="hideDialog">
		<el-form :model="editForm" label-width="85px" :rules="editFormRules" ref="editForm" id="editForm">
			<el-row>
				<el-col :span="24">
					<el-form-item label="" prop="name">
						<el-input v-model="editForm.name" name="name" auto-complete="off" placeholder=""></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="24">
					<el-form-item label="" prop="code">
						<el-input v-model="editForm.code" name="code" auto-complete="off" placeholder="" :disabled="inputdisabled"></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="24">
					<el-form-item label="" prop="type">
						<el-select v-model="editForm.type" filterable placeholder="" style="width: 100%;">
							<el-option v-for="item in options" :label="item.name" :value="item.id"></el-option>
						</el-select>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="24">
					<el-form-item label="" prop="enabled">
						<el-radio-group v-model="editForm.enabled">
							<el-radio label="true"></el-radio>
							<el-radio label="false"></el-radio>
						</el-radio-group>
					</el-form-item>
				</el-col>
			</el-row>

			<el-row>
				<el-col :span="24">
					<el-form-item label="" prop="score">
						<el-input v-model="editForm.score" name="score" auto-complete="off" placeholder=""></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="24">
					<el-form-item label="" prop="notes">
						<el-input type="textarea" v-model="editForm.notes" resize="none" placeholder=""></el-input>
					</el-form-item>
				</el-col>
			</el-row>

		</el-form>
		<div slot="footer" class="dialog-footer">
			<el-button @click.native="hideDialog"></el-button>
			<el-button type="primary" @click.native="editSubmit" :loading="editLoading">{{btnEditText}}</el-button>
		</div>
	</el-dialog>
</template>

<script>
    import indicatorsManager from '../../api/indicatorsManager'
    export default {
        data() {
            var ValidatorCode = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else if(!this.$regExp.number.test(value)){
                    callback(new Error(''));
                } else {
                    callback();
                }
            };
            var ValidatorScore = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else if(!this.$regExp.numberdS.test(value)){
                    callback(new Error(''));
                } else {
                    callback();
                }
            };
            return {
                type:'',
                imageUrl:'',
                editFormVisible: false,//
                editFormTtile: '',//
                inputdisabled:false,//input
                options: [],
                editFormRules: { //
                    name: [
                        { required: true, message: ''}
                    ],
                    code: [
                        {required: true, validator: ValidatorCode}
                    ],
                    enabled: [
                        { required: true, message: ''}
                    ],
                    score:[
                        {required: true, validator: ValidatorScore}
                    ],
                    type:[
                        { required: true, message: ''}
                    ],
                    notes:[
                        { min: 0, max:200, message: '200' }
                    ]
                },
                //
                editForm: {
                    id: 0,
                    name: '',
                    code: '',
                    type:'',
                    notes:'',
                    score:'',
                    enabled:'',
                },
                editLoading: false,
                btnEditText: '',
            };
        },
        created(){
            this.$bus.$off('IndicatorsDialogShow');
            this.$bus.$on('IndicatorsDialogShow', (title,data,type) => {
                this.type=type;
                this.IndicatorsDialogShow(title,data);
            })
        },
        methods: {
            hideDialog(){
                this.editFormVisible = false;
                setTimeout(() =>{
                    this.$refs['editForm'].resetFields();
                    this.editForm.expression='';
                },100)
            },
            //dialog
            IndicatorsDialogShow:function(title,data){
                this.inputdisabled=false;
                this.editFormVisible = true;
                this.editFormTtile = title;
                if(data.id){
                    this.editFormInput=false;//
					this.inputdisabled=true;
                    this.reload(data);
                }
            },
            reload:function(data){
                var _this = this;
                indicatorsManager.getInfo(data).then((res) => {
                    _this.editForm = res.result;
                    _this.editForm.type = parseInt(res.result.type);
                    _this.editForm.enabled =String(res.result.enabled);
                }).catch(function (error) {
                    _this.$Error(_this,error);
                });
            },
            getoTypesptions(){//
                var params = {
                    type:'FACT',
                    parentId:'-1'
                }
                indicatorsManager.getoTypesptions(params).then(res=>{
                    if(res.code=="-1"){
						this.$notify({
							title: '',
							message: res.msg,
							type: 'error'
						});
					}else{
						this.options = res;
					}
            	}).catch(function (error) {
                    this.$Error(this,error);
                })
            },
            // or 
            editSubmit: function () {
                var _this = this;
                _this.$refs.editForm.validate((valid) => {
                    if (valid) {
                        _this.editLoading = true;
                        _this.btnEditText = '';
                        if (_this.type == "add") {
                            //
                            var params = {
                                name: _this.editForm.name,
                                code: _this.editForm.code,
                                type:_this.editForm.type,
                                notes:_this.editForm.notes,
                                score:_this.editForm.score,
                                enabled:_this.editForm.enabled
                            };
                            indicatorsManager.add(params).then((res) => {
                                _this.editLoading = false;
                            _this.btnEditText = '';
                            _this.editFormVisible = false;
                            _this.$message({
                                message: "",
                                type: 'success'
                            });
                            _this.$bus.$emit('updataIndicatorsList');
                        }).catch(function (error) {
                                _this.editLoading = false;
                                _this.btnEditText = '';
                                _this.$Error(_this,error);
                            });
                        } else if (_this.type == "edit") {
                            //
                            var params = {
                                id: _this.editForm.id,
                                name: _this.editForm.name,
                                code: _this.editForm.code,
                                type:_this.editForm.type,
                                notes:_this.editForm.notes,
                                score:_this.editForm.score,
                                enabled:_this.editForm.enabled
                            };
                            indicatorsManager.editInfo(params).then((res) => {
                                _this.editLoading = false;
                            _this.btnEditText = '';
                            _this.editFormVisible = false;
                            _this.$message({
                                message: "",
                                type: 'success'
                            });
                            _this.$bus.$emit('updataIndicatorsList');
                        }).catch(function (error) {
                                _this.editLoading = false;
                                _this.btnEditText = '';
                                _this.$Error(_this,error);
                            });
                        }
                       /* _this.$confirm('', '', {}).then(() => {

                        }).catch(() => {});*/
                    }
                });
            }
        },
        mounted() {
            var _this=this;
            _this.getoTypesptions();
        }
    };
</script>

<style scoped>
	.line{ text-align: center; font-size: 14px;}
</style>



