<!------>
<template>
	<el-dialog :title="editFormTtile" size="small" v-model="editFormVisible" :close-on-click-modal="false">
		<el-form :model="editForm" label-width="140px" :rules="editFormRules" ref="editForm" id="editForm">
			<el-row>
				<el-col :span="12">
					<el-form-item label="" prop="mobiles">
						<el-input v-model="editForm.mobiles" placeholder="" name="mobiles" auto-complete="off"></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="" prop="personName">
						<el-input v-model="editForm.personName" placeholder="" name="personName" auto-complete="off"></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="12">
					<el-form-item label="" prop="personIdCard">
						<el-input v-model="editForm.personIdCard" placeholder="" name="personIdCard" auto-complete="off"></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="" prop="companyName">
						<el-input v-model="editForm.companyName" placeholder="" name="companyName" auto-complete="off"></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="12">
					<el-form-item label="" prop="isLeader">
						<el-radio-group v-model="editForm.isLeader">
							<el-radio label="1"></el-radio>
							<el-radio label="0"></el-radio>
						</el-radio-group>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="" prop="maxLendNorm">
						<el-input placeholder="" v-model="editForm.maxLendNorm" name="maxLendNorm" auto-complete="off">
							<template slot="append"></template>
						</el-input>
					</el-form-item>
				</el-col>
			</el-row>

			<el-row>
				<el-col :span="12">
					<el-form-item label="" prop="maxSingleLendNorm">
						<el-input placeholder="" v-model="editForm.maxSingleLendNorm" name="maxSingleLendNorm" auto-complete="off">
							<template slot="append"></template>
						</el-input>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="" prop="maxLoanTerm">
						<el-input placeholder="" v-model="editForm.maxLoanTerm" name="maxLoanTerm" auto-complete="off">
							<template slot="append"></template>
						</el-input>
					</el-form-item>
				</el-col>
			</el-row>

			<el-row>
				<el-col :span="12">
					<el-form-item label="">
						<el-input placeholder="" v-model="editForm.import" :disabled="true" auto-complete="off">
							<template slot="append"></template>
						</el-input>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="">
						<el-input placeholder="" v-model="editForm.availableCredit" :disabled="true" auto-complete="off">
							<template slot="append"></template>
						</el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="12">
					<el-form-item label="">
						<el-input placeholder="" v-model="editForm.frozenCredit" :disabled="true" auto-complete="off">
							<template slot="append"></template>
						</el-input>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="" prop="name">
						<el-date-picker v-model="value1" type="date" placeholder="" :picker-options="pickerOptions0" :editable="false"></el-date-picker>
					</el-form-item>
				</el-col>
			</el-row>

		</el-form>
		<div slot="footer" class="dialog-footer">
			<el-button @click.native="editFormVisible = false"></el-button>
			<el-button type="primary" @click.native="editSubmit" :loading="editLoading">{{btnEditText}}</el-button>
		</div>
	</el-dialog>
</template>

<script>
    import nameManager from '../../api/nameManager'
    export default {
        data() {
            var maxLoanTerm = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else if(!this.$regExp.number.test(value)){
                    callback(new Error(''));
                } else {
                    callback();
                }
            };
            //maxSingleLendNorm
            var maxSingleLendNorm = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else if(!this.$regExp.amountPlus.test(value)){
                    callback(new Error(''));
                } else {
                    callback();
                }
            };

            var maxLendNorm = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else if(!this.$regExp.amountPlus.test(value)){
                    callback(new Error(''));
                } else {
                    callback();
                }
            };

            var phone =  (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else if(!this.$regExp.phone.test(value)){
                    callback(new Error(''));
                } else {
                    callback();
                }
            };
            return {
                value1: '',
                pickerOptions0: {
                    disabledDate(time) {
                        return time.getTime() < Date.now() - 8.64e7;
                    }
                },
                imageUrl:'',
                editFormVisible: false,//
                editFormTtile: '',//
                editFormInput:true,//input
                //
                editForm: {
                    id: 0,
                    mobiles: '',
                    personName: '',
                    personIdCard:'',
                    companyName:'',
                    isLeader:'',
                    maxLendNorm:'',
                    effectiveTerm:'',
                    maxLoanTerm:'',
                    maxSingleLendNorm:'',
                    status:''
                },
                editLoading: false,
                btnEditText: '',

                editFormRules: { //
                    mobiles: [
                        { required: true, validator: phone}
                    ],
                    personName: [
                        { required: true, message: ''}
                    ],
                    personIdCard: [
                        { required: true, message: ''}
                    ],
                    companyName: [
                        { required: true, message: ''}
                    ],
                    isLeader: [
                        { required: true, message: ''}
                    ],
                    maxSingleLendNorm: [
                        { required: true,validator:maxSingleLendNorm}
                    ],
                    maxLoanTerm: [
                        {required: true, validator: maxLoanTerm}
                    ],
                    effectiveTerm: [
                        { required: true, message: ''}
                    ],
                    maxLendNorm:[
                        { required: true, validator: maxLendNorm}
					]


                }
            };
        },
        created(){
            this.$bus.$off('companyDialogShow');
            this.$bus.$off('H_userDialogShow');
            this.$bus.$on('H_userDialogShow', (title,data,api) => {
                //
                this.DialogShow(title,data,api);
            })
        },
        methods: {
            //dialog
            DialogShow:function(title,data,api){
                this.editFormVisible = true;
                this.editFormTtile = title;
                this.api = api || '';
                if(data.id){
                    this.editFormInput=false;//
                    this.reload(data);
                }
            },
            reload:function(data){
                this.editForm = data;
                this.editForm.isLeader = this.editForm.isLeader=="1"?"1":"0";
            },
            // or 
            editSubmit: function () {
                var _this = this;

                _this.$refs.editForm.validate((valid) => {
                    if (valid) {

                        /*_this.$confirm('', '', {}).then(() => {


                        });*/

                        _this.editLoading = true;
                        _this.btnEditText = '';
                        if (_this.editForm.id == 0) {
                            //
                            var params = {
                                mobiles: _this.editForm.mobiles,
                                realName: _this.editForm.realName,
                                password: _this.editForm.password,
                                confirmPassword:_this.editForm.confirmPassword,
                                mobile: _this.editForm.mobile,
                                email: _this.editForm.email,
                                workNumber: _this.editForm.workNumber
                            };
                            nameManager.personwhite_add(params).then((res) => {
                                _this.editLoading = false;
                            _this.btnEditText = '';
                            _this.editFormVisible = false;
                            _this.$message({
                                message: "",
                                type: 'success'
                            });
                            _this.$bus.$emit('updatagOperator');
                        }).catch(function (error) {
                                _this.editLoading = false;
                                _this.btnEditText = '';
                                _this.$Error(_this,error);
                            });
                        } else {
                            if(_this.api==='pnormallist_put'){
                               // debugger;
                                //
                                nameManager.pnormallist_put(_this.editForm).then((res) => {
                                    _this.editLoading = false;
                                _this.btnEditText = '';
                                _this.editFormVisible = false;
                                _this.$message({
                                    message: "",
                                    type: 'success'
                                });
                                _this.$bus.$emit('H_userList_reload');
                            }).catch(function (error) {
                                    _this.editLoading = false;
                                    _this.btnEditText = '';
                                    _this.$Error(_this,error);
                                });
                            }else{
                                //
                                _this.editForm.maxLoanTermType = 1; //3:2:1 
                                nameManager.personwhite_update(_this.editForm).then((res) => {
                                    _this.editLoading = false;
                                _this.btnEditText = '';
                                _this.editFormVisible = false;
                                _this.$message({
                                    message: "",
                                    type: 'success'
                                });
                                _this.$bus.$emit('H_userWhite_reload');
                            }).catch(function (error) {
                                    _this.editLoading = false;
                                    _this.btnEditText = '';
                                    _this.$Error(_this,error);
                                });
                            }


                        }

                    }
                });

            }
        }
    };
</script>

<style scoped>
	.el-form-item__content{ font-size: 50px;}
</style>




