<template>
	<div class="box" style="min-width: 1000px;">
		<!--id="editor"-->
		<div id="editor">
			<!--  echarts-->
			<div class="h-box">
				<el-row>
					<el-col :span="12" style="position: relative;">
						<el-form  label-width="150px">
							<el-form-item label="">
								<span>{{topData.applyNum}}</span>
							</el-form-item>
							<el-form-item label="" class="h-chartR">
								<div id="mainChart" style="width:400px; height:300px;"></div>
							</el-form-item>
						</el-form>
					</el-col>
					<el-col :span="12">
						<el-form  label-width="150px">
							<el-form-item label="">
								<span>{{topData.createTime}}</span>
							</el-form-item>
							<el-form-item label="">
								<span>{{topData.ruleDecision}}</span>
							</el-form-item>
							<el-form-item label="">
								<span>{{topData.overallDecision}}</span>
							</el-form-item>
							<el-form-item label="">
								<span>{{topData.suggestAmount}}</span>
							</el-form-item>
						</el-form>
					</el-col>
				</el-row>
			</div>

			<!---->
			<div class="h-box">
				<h3 class="h-title" style="margin: 0; padding: 0;"></h3>
				<el-row>
					<el-col :span="12">
						<el-form  label-width="150px">
							<el-form-item label="">
								<span>{{customerData.realName}}</span>
							</el-form-item>
							<el-form-item label="">
								<span>{{customerData.mobile}}</span>
							</el-form-item>
						</el-form>
					</el-col>
					<el-col :span="12">
						<el-form  label-width="150px">
							<el-form-item label="">
								<span>{{customerData.idCard}}</span>
							</el-form-item>
							<el-form-item label="">
								<span>{{customerData.bankCard}}</span>
							</el-form-item>
						</el-form>
					</el-col>
				</el-row>

				<!---->
				<table class="h-table" style="width: 100%;">
					<thead class="h-thead">
					<tr>
						<th height="40px" colspan="2" style="font-size: 16px;"></th>
					</tr>
					</thead>
					<tbody class="h-tbody">
					<tr>
						<th width="40%"></th>
						<th width="60%"></th>
					</tr>
					<template v-if="preLoanData.length > 0">
						<tr v-for="item in preLoanData" >
							<td width="40%">{{item.type}}</td>
							<td width="60%">{{item.hitNum}}</td>
						</tr>
					</template>
					<template v-else>
						<tr >
							<td colspan="2" style="text-align: center; color: #999;"></td>
						</tr>
					</template>
					</tbody>
				</table>

				<!-- ---->
				<h1 class="h-report-mainTitle"></h1>
				<h2 class="h-report-subTitle"></h2>
				<table class="h-table" style="width: 100%;">
					<tbody class="h-tbody" style="table-layout: fixed;">
					<tr>
						<td width="150" class="h-bgeee"></td>
						<td width="">{{IDVerification.realName}}</td>
						<td width="200" rowspan="4">
							<img src="../../../static/images/idDefault.png" alt="" title="">
						</td>
					</tr>
					<tr>
						<td class="h-bgeee"></td>
						<td>{{IDVerification.idCard}}</td>
					</tr>
					<tr>
						<td class="h-bgeee"></td>
						<td>{{IDVerification.result}}</td>
					</tr>
					<tr>
						<td class="h-bgeee"></td>
						<td>{{IDVerification.attribution}}</td>
					</tr>
					</tbody>

				</table>

				<!---->
				<h1 class="h-report-mainTitle"></h1>
				<div v-if="hitRuleData.length > 0">
					<template v-for="item in hitRuleData">
						<h2 class="h-report-subTitle">{{item.type}}</h2>
						<table class="h-table" style="width: 100%; table-layout: fixed;">
							<tbody class="h-tbody">
							<tr>
								<th width="33%"></th>
								<th width="33%"></th>
								<th width=""></th>
							</tr>
							<template v-if="item.hitRuleDetail.length >0">
								<tr v-for="key in item.hitRuleDetail">
									<td>{{key.type}} </td>
									<td>{{key.name}}</td>
									<td>{{key.decision}}</td>
								</tr>
							</template>
							<template v-else>
								<tr >
									<td colspan="3" style="text-align: center; color: #999;"></td>
								</tr>
							</template>

							</tbody>
						</table>
					</template>
				</div>
				<div v-else>
					<div  class="h-noData"></div>
				</div>

				<!---->
				<h1 class="h-report-mainTitle"></h1>
				<div v-if="hitRatingData.length >0">
					<template v-for="item in hitRatingData">
						<h2 class="h-report-subTitle">{{item.type}}</h2>
						<table class="h-table" style="width: 100%; table-layout: fixed;">
							<tbody class="h-tbody">
							<tr>
								<th width="33%"></th>
								<th width="33%"></th>
								<th width=""></th>
							</tr>
							<template v-if="item.hitFactDetail.length >0">
								<tr v-for="key in item.hitFactDetail">
									<td>{{key.type}} </td>
									<td>{{key.name}}</td>
									<td>{{key.score}}</td>
								</tr>
							</template>
							<template v-else>
								<tr >
									<td colspan="3" style="text-align: center; color: #999;"></td>
								</tr>
							</template>
							</tbody>
						</table>
					</template>
				</div>
				<div v-else>
					<div  class="h-noData"></div>
				</div>

				<!--end-->

			</div>

		</div>


		<!---->
		<el-row class="h-scoreSet-btnBox" style="margin-left: 16px; text-align: center;">
			<el-button  icon="d-arrow-left" @click="gotoBack"></el-button>
			<el-button type="primary" icon="document" @click="exportPdf">PDF</el-button>
		</el-row>


		<div id="test" style="margin-top: 50px;">

		</div>

	</div>
</template>

<script>
	import echarts from 'echarts'
	import riskReport from '../../api/riskReport'
	export default{
		data (){
			return {
				chartColumn:null,
				//
				topData:{
					applyNum:'',       //
					createTime:'',     //
					ruleDecision:'',  //
					overallDecision:'', //
					suggestAmount:'' //
				},
				//
				customerData:{
					realName:'',//
					mobile:'',//
					idCard:'',//
					bankCard:''//
				},
				//
				preLoanData:[],
				//
				IDVerification:{
					realName:'',//
					idCard:'',//
					attribution:'',//,
					photo:'', //
					result:''//
				},
				//
				hitRuleData:[],
				//
				hitRatingData:[],
				//
				result:{
					"applyNum": 1492504422441,
					"point": 40,
					"triggerLoggers": [
						"---1",
						"--25~35-3",
						"--6-RD",
						"--2-D"
					],
					"suggestAmount": 280,
					"level": "E",
					"decision": "SD-",
					"ruleDecision": "D-",
					"overallDecision": "D-",
					"createTime": "2017-04-18 08:33:43",
					"userType": "NORMAL",
					"summaryList": [
						{
							"type": "",
							"hitNum": 0
						},
						{
							"type": "",
							"hitNum": 2
						},
						{
							"type": "",
							"hitNum": 0
						}
					],
					"certification": {
						"realName": "",
						"idCard": "440823199010051234",
						"result": "",
						"attribution": ""
					},
					"hitRule": [
						{
							"type": "",
							"hitRuleDetail": [
								{
									"type": "",
									"name": "6",
									"decision": "RD-"
								},
								{
									"type": "",
									"name": "2",
									"decision": "D-"
								}
							]
						}
					],
					"hitFact": [
						{
							"type": "",
							"hitFactDetail": [
								{
									"type": "",
									"name": "",
									"score": "1"
								},
								{
									"type": "",
									"name": "25~35",
									"score": "3"
								}
							]
						}
					],
					"realName": "",
					"mobile": "13434636718",
					"bankCard": "6222023654258849987",
					"idCard": "440823199010051234"
				},
			}
		},
		methods:{
			//pdf
			exportPdf(){
				var _this = this;
				var id = _this.$route.query.id;
				if(!id) return;
				riskReport.ajaxPdf(id).then(res =>{
					if(res.code !='-1'){
					riskReport.exportPdf(id);
				}
			}).catch(error =>{
					_this.$Error(_this,error);
			})
			},
			//
			getPersonReport(){
				var _this = this;
				var params = {
					custAppkey:_this.$route.query.custAppkey,
					productCode:_this.$route.query.productCode,
					personId:_this.$route.query.personId
				};
				var id = _this.$route.query.id;
				if(!id) return;
				riskReport.getPersonReport(id).then(res =>{
					if(res.code =='0'){
						_this.result =  eval("("+res.result.content+")");
						_this.demoInit(_this.result);
					    var obj = res.result;
						//
						_this.customerRender({
							realName:obj.userName,       //
							mobile:obj.phone,     //
							idCard:obj.idCard,   //
							bankCard:obj.bankCard
						});
					}else{
						_this.$message({
							type: 'warning',
							message: res.msg || ''
						});
					}
				}).catch(error =>{
				 _this.$Error(_this,error)
				});
			},

			gotoBack(){
				this.$router.push({ path: '/customerList'});
			},
			//
			chartInit(parmas){
				var parmas = typeof parmas =='object'?parmas :{};
				var obj = {
					min:parmas.min || 0,
					max:parmas.max ||1000,
					name:parmas.name || '',
					type:'gauge',
					data:[{value:parmas.point || 0,name:parmas.level || ''}] // pointlevel
				};
				var arr = [];
				arr.push(obj);
				this.chartColumn = echarts.init(document.getElementById('mainChart'));
				this.chartColumn.setOption({
					/*tooltip : {
						formatter: "{a} <br/>{b} : {c}"
					},*/
					series: arr
				});
			},
			//
			topDataRender(params){
				this.topData = {
					applyNum:params.applyNum ||' ',       //
					createTime:params.createTime ||' ',     //
					ruleDecision:params.ruleDecision ||' ',  //
					overallDecision:params.overallDecision ||' ', //
					suggestAmount:this.$Utils.addThousandSign(params.suggestAmount,2)//
				}
			},
			//
			customerRender(params){
				this.customerData = {
					realName:params.realName ||' ',       //
					mobile:params.mobile ||' ',     //
					bankCard:params.bankCard ||' ',  //
					idCard:params.idCard ||' '  //
				}
			},
			//
			idVerificationRender(params){
				this.IDVerification = {
					realName:params.realName,//
					idCard:params.idCard,//
					attribution:params.attribution || ' ',//,
					photo:params.photo || '', //
					result:params.result//
				}
			},
			//
			preLoanRender(data){
				for(var i= 0,len = data.length; i<len; i++){
					if(data[i].val ==null || data[i].val ==''){
						data[i].val = ' ';
					}
				}
				this.preLoanData =  data;
			},
			//
			hitRuleRender(data){
				this.hitRuleData = data;
			},
			//
			hitRatingRender(data){
				this.hitRatingData = data;
			},



			exportPDF(){
				/*var doc = new jsPDF();
				doc.text('Hello world!', 10, 10);
				doc.save('a4.pdf')*/

				/*html2canvas(document.body).then(function(canvas) {
					//document.body.appendChild(canvas);
					document.getElementById('test').appendChild(canvas);
				});*/

				html2canvas(document.getElementById('editor')).then(function(canvas) {
					//document.body.appendChild(canvas);
					//canvas.setAttribute('id','demo');

					//document.getElementById('test').appendChild(canvas);

					/*var imgData = canvas.toDataURL('img/notice/png');
					var doc = new jsPDF('p', 'px','a3');
					//        
					doc.addImage(imgData, 'PNG', -9, 0,650,1500);
					doc.addPage();
					doc.addImage(imgData, 'PNG', -9, -900,650,1500);
					doc.save('test.pdf');*/


					//var image    = canvas.toDataURL("image/png");
					//var w=window.open('about:blank','image from canvas');
					//w.document.write("<img src='"+image+"' alt='from canvas'/>");

					var image = canvas.toDataURL("image/png");
					var pHtml = "<img src="+image+" title='canvas' />";
					document.getElementById('test').innerHTML = pHtml;


				});

			},

			//
			demoInit(){
				var _this = this;
				this.chartInit({point:_this.result.point||'',level:_this.result.level||''});
				this.topDataRender(_this.result); ///
				this.preLoanRender(_this.result.summaryList); //
				this.customerRender({
					realName:_this.result.realName,       //
					mobile:_this.result.mobile,     //
					bankCard:_this.result.bankCard,  //
					idCard:_this.result.idCard    //
				});
				this.idVerificationRender(_this.result.certification); //
				this.hitRuleRender(_this.result.hitRule); //
				this.hitRatingRender(_this.result.hitFact); //
			},
		},
		mounted:function(){
			this.getPersonReport();
			//this.demoInit();

		}

	}
</script>

<style scoped>
	.chart {
		width: 100%;
		float: left;
	}
	/*.chart div {
        height: 400px;
        float: left;
    }*/

	#mainChart{position: absolute; left: -70px; top: -40px;}
	.el-col {
		padding: 30px 20px;
	}
	.box{ position: relative; width: 95%; margin: 0 auto; clear: both; overflow: hidden;}
	.h-box{position: relative; width: 100%; clear: both; overflow: hidden; margin-bottom: 25px;}
	.h-box .h-title{text-align: center; height: 50px; line-height: 35px; border-bottom: 1px solid #ddd; font-size: 15px; }
	.h-table{
		color:#333;
		border-width: 1px;
		border-color: #ddd;
		border-collapse: collapse;
	}
	.h-table th{
		border-width: 1px;
		padding: 8px;
		border-style: solid;
		border-color: #ddd;
		background-color: #eee;
	}
	.h-table td{
		border-width: 1px;
		padding: 8px;
		border-style: solid;
		border-color: #ddd;
		background-color: #fff;
		text-align: center;;
	}

	.h-report-mainTitle{height: 57px; width: 100%; text-align: center; background-color:#eee; font-size: 16px; line-height: 57px; margin-top: 40px;}

	.h-report-subTitle{height: 30px; width: 100%; text-align: center; background-color:#fff; font-size: 15px; line-height: 30px;  }
	.h-table td.h-bgeee{background-color:#eee; text-align: right;}

	.h-report-divBox{border: 1px solid #ddd; position: relative; overflow: hidden; clear: both; padding: 15px; }
	.h-report-divBox .h-lineRow{ position: relative; overflow: hidden; clear: both;}
	.h-lineRow .h-spanName{  text-align: right; line-height: 45px;}
	.h-lineRow .h-fl{float: left; padding-left: 150px;}
	.h-Ttn{ text-align: right; vertical-align: top;}
	.h-Ttc{padding-bottom: 35px; line-height: 25px;}

	.h-noData{border: 1px solid rgb(204, 204, 204);
		height: 50px;
		line-height: 50px;
		margin-top: -16px; text-align: center; color: #999;}

	.el-col{
		padding: 10px 20px;
	}
</style>


