<template>
	<section>
		<!---->
		<el-row >
			<el-form label-width="120px">

				<el-col :span="6">
					<el-form-item label="" :inline="true"> <el-input  placeholder="" v-model="filters.name"></el-input></el-form-item>
				</el-col>

				<el-col :span="6">
					<el-form-item label="" :inline="true">
						<el-date-picker
								v-model="filters.dataRange"
								type="daterange"
								:picker-options="pickerOptions"
								placeholder=""
								:editable = false
								@change="getData"
								align="right">
						</el-date-picker>
					</el-form-item>
				</el-col>



				<el-col :span="6" :offset="6">
					<el-form-item class="btn_box">
						<el-button type="primary" icon="search" v-on:click="search()"></el-button>
					</el-form-item>
				</el-col>
			</el-form>
		</el-row>

		<el-row class="button_box">
			<el-button  icon="document" class="btn_border_none"  @click="handleDownload"></el-button>
		</el-row>

		<el-table :data="tableData" border style="width: 100%" v-loading="listLoading">
			<el-table-column prop="name" label="" min-width="130" show-overflow-tooltip></el-table-column>
			<el-table-column prop="createTime" label="" width="180" show-overflow-tooltip></el-table-column>
			<el-table-column prop="updateTime" label="" width="180" show-overflow-tooltip></el-table-column>
			<el-table-column prop="enableRuleNum" label="" width="120" show-overflow-tooltip></el-table-column>
			<el-table-column prop="enableFactNum" label="" width="120" show-overflow-tooltip></el-table-column>
			<el-table-column prop="maxProductCreditAmount" label=""  min-width="120" show-overflow-tooltip></el-table-column>
			<el-table-column prop="maxUserCreditAmount" label="" min-width="120" show-overflow-tooltip></el-table-column>
			<el-table-column prop="passRate" label="" width="130" show-overflow-tooltip></el-table-column>
			<el-table-column prop="refuseRate" label="" width="120" show-overflow-tooltip></el-table-column>
			<el-table-column prop="enableWhiteList" label="" :formatter="format_enableWhiteList" width="100" show-overflow-tooltip></el-table-column>
			<el-table-column prop="reportNum" label="" width="100" show-overflow-tooltip></el-table-column>
			<el-table-column prop="creditUserNum" label="" width="100" show-overflow-tooltip></el-table-column>
		</el-table>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom:10px;">
			<el-pagination @current-change="handleCurrentChange" :page-size="10" layout="total, prev, pager, next, jumper" :total="total" style="float:right;"></el-pagination>
		</el-col>
	</section>
</template>
<script>
	import productAnalysisApi from  '../../api/productAnalysis'
	export default {
		data() {
			return {
				pickerOptions: {
					shortcuts: [{
						text: '',
						onClick(picker) {
							const end = new Date();
							const start = new Date();
							start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
							picker.$emit('pick', [start, end]);
						}
					}, {
						text: '',
						onClick(picker) {
							const end = new Date();
							const start = new Date();
							start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
							picker.$emit('pick', [start, end]);
						}
					}, {
						text: '',
						onClick(picker) {
							const end = new Date();
							const start = new Date();
							start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
							picker.$emit('pick', [start, end]);
						}
					}]
				},
				filters: {
					dataRange:'',
					name:''
				},
				tableData:[],
				total: 0,
				page: 1,
				listLoading: false,
				formatterData:''
			};
		},
		watch: {

		},
		methods: {
			handleDownload(){
				if(this.formatterData){
					var beginDate = this.formatterData.slice(0,10);
					var endDate = this.formatterData.substr(this.formatterData.length-10);
				}else{
					var beginDate = '';
					var endDate = '';
				};
				productAnalysisApi.export({
					beginDate: beginDate,
					endDate:endDate,
					name:this.filters.name
				});
			},
			handleCurrentChange(val) {
				this.page = val;
				this.search();
			},
			search(){
				var _this = this;
				if(this.formatterData){
					var beginDate = this.formatterData.slice(0,10);
					var endDate = this.formatterData.substr(this.formatterData.length-10);
				}else{
					var beginDate = '';
					var endDate = '';
				}
				var params = {
					pageNum:_this.page, //
					pageSize:10,   //10
					beginDate: beginDate,
					endDate:endDate,
					name:_this.filters.name
				};
				_this.listLoading = true;
				productAnalysisApi.getProductAnalysis(params)
						.then(res => {
					_this.total = res.totalElements;

					for(var i = 0,len = res.content.length; i < len; i++){
						if(res.content[i].maxProductCreditAmount){
							res.content[i].maxProductCreditAmount = _this.$Utils.addThousandSign(res.content[i].maxProductCreditAmount,0);
						}
						if(res.content[i].maxUserCreditAmount){
							res.content[i].maxUserCreditAmount = _this.$Utils.addThousandSign(res.content[i].maxUserCreditAmount,0);
						}
					}
				_this.tableData = res.content;
				//
				_this.listLoading = false;
			})
			.catch(function (error) {
					_this.listLoading = false;
					_this.$Error(_this,error);
				})
			},
			//
			getData(val){
				this.formatterData = val;
			},
			format_enableWhiteList(row,col){
				if(row.enableWhiteList =='1'){
					return '';
				}else{
					return '';
				}
			}
		},
		mounted() {
			this.search();
		}
	}
</script>
<style scoped>

</style>


