<!-- ---- 2017/3/21 pm lhw-->
<template>
	<el-dialog :title="editFormTtile" size="small" v-model="editFormVisible" :close-on-click-modal="false">
        <section>


            <el-table :data="Data"  border highlight-current-row  @current-change="handleCurrentChange"  style="width: 100%; margin-top: 10px;" v-loading="listLoading">
                <el-table-column prop="name" width="150" label="" show-overflow-tooltip></el-table-column>
                <el-table-column prop="notes" label="" min-width="150"  show-overflow-tooltip></el-table-column>
            </el-table>

        </section>
		<div slot="footer" class="dialog-footer">
			<el-button @click.native="editFormVisible = false"></el-button>
			<el-button type="primary" @click.native="editSubmit"></el-button>
		</div>
	</el-dialog>
</template>

<script>
    import productList from '../../api/productList' //API
    export default {
        data() {
            return {
                productId:'', //id
                type:'',
                editFormVisible: false,//
                editFormTtile: '',//
                searchinput: '',
                num:'10',
                listLoading:false,
                currentRow: null,
                Data:[
                   /* {
                        "id": 69863,
                        "name": "R",
                        "notes": "aaaddd"
                    },
                    {
                        "id": 69864,
                        "name": "RD",
                        "notes": ""
                    },
                    {
                        "id": 69865,
                        "name": "D",
                        "notes": ""
                    }*/
                ],
                editLoading: false,
                btnEditText: ''
            };
        },
        created(){

            this.$bus.$off('optionsResultSet_addPage_rule');
            this.$bus.$on('optionsResultSet_addPage_rule', (title,data) => {
                this.optionsResultSet_addPage_rule(title,data);
            });

            //
            this.$bus.$off('optionsResultSet_getRuleList');
            this.$bus.$on('optionsResultSet_getRuleList', (params) => {
                this.search(params);
        })

        },
        methods: {
            //dialog
            optionsResultSet_addPage_rule:function(title,data){
                this.editFormVisible = true;
                this.editFormTtile = title;
                this.type = data.type;
                this.productId = data.productId;
                this.search({gradeResult:data.gradeResult});
            },
            reload:function(data){
                this.editForm = data;
            },
            //
            handleCurrentChange(val) {
                this.currentRow = val;
            },
            //
            handleIconClick(ev) {

            },

            //
            editSubmit: function () {
                var _this = this;
               // console.log(_this.currentRow);
                if(_this.currentRow){
                    _this.$bus.$emit('H_optionsResultSet_bindData',_this.type,_this.currentRow.name);
                    _this.editFormVisible = false;
                }else{
                    _this.$message({
                        type:'warning',
                        message:''
                    })
                }

            },
            search(obj){
                var obj = typeof obj =='object'? obj : {gradeResult:''};
                var _this = this;
                var params = {
                    productId:_this.$route.query.productId,
                    gradeResult:obj.gradeResult || '' //
                };
                _this.listLoading = true;
                productList.rulelist_get(params).then(res =>{
                    _this.Data = res;
                _this.listLoading = false;
                }).catch(error =>{
                        _this.$Error(_this,error);
                _this.listLoading = false;
                });
            },
        },
        mounted() {

        }
    };
</script>

<style scoped>
	.line{ text-align: center; font-size: 14px;}
</style>



