<template>
    <div  v-loading="boxLoading">
            <el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm" id="editForm">
                <div class="h-rule-edit-box clearfix">
                    <div class="h-rule-edit-title"></div>
                    <el-row>
                        <div style="width: 640px;">
                            <el-form-item label="" prop="name">
                                <el-input v-model="editForm.name" name="name" placeholder="" auto-complete="off"></el-input>
                            </el-form-item>
                        </div>
                    </el-row>
                    <el-row>
                        <el-form-item label="" prop="code">
                            <el-input v-model="editForm.code" name="code" class="h-ruleInput" placeholder="" :disabled="true" auto-complete="off"></el-input>
                        </el-form-item>
                    </el-row>

                    <el-row>
                        <el-col :span="24">
                            <el-form-item label="" required>
                                <el-row>
                                    <div style="width: 250px; float: left;">
                                        <el-form-item prop="types">
                                            <el-select v-model="editForm.types" filterable placeholder="" class="h-ruleInput"  :disabled="true">
                                                <el-option v-for="item in options" :label="item.name" :value="item.id"></el-option>
                                            </el-select>
                                        </el-form-item>
                                    </div>
                                    <el-col class="line"  style="width: 50px; float: left;"></el-col>
                                    <div style="width: 250px; float: left;">
                                        <el-form-item prop="smallTypes">
                                            <el-select v-model="editForm.smallTypes" filterable placeholder="" class="h-ruleInput"  :disabled="true">
                                                <el-option v-for="item in subsetoptions" :label="item.name" :value="item.id"></el-option>
                                            </el-select>
                                        </el-form-item>
                                    </div>
                                </el-row>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="" prop="enabled">
                                <el-radio-group v-model="editForm.enabled">
                                    <el-radio label="true"></el-radio>
                                    <el-radio label="false"></el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-form-item label="" prop="weight">
                            <el-input v-model="editForm.weight" class="h-ruleInput"  placeholder=""></el-input>
                        </el-form-item>
                    </el-row>

                    <el-row>
                        <div style="width: 640px;">
                            <el-form-item label="" prop="notes">
                                <el-input type="textarea" v-model="editForm.notes"  placeholder="" resize="none"></el-input>
                            </el-form-item>
                        </div>
                    </el-row>
                </div>

                <div class="h-rule-edit-box clearfix">
                    <div class="h-rule-edit-title"></div>
                    <devSetting  ref="child1"></devSetting>
                </div>

                <div class="h-rule-edit-box clearfix">
                    <div class="h-rule-edit-title"></div>
                    <resSetting></resSetting>
                </div>

                <div class="h-rule-edit-box clearfix" v-show="RuleSetting">
                    <div class="h-rule-edit-title"></div>
                    <el-row >
                        <el-col :span="20" style="width: 950px;">
                            <el-form-item label="" prop="expression">
                                <v-editor :input-content="inputContent" :upload-url="uploadUrl" v-model="editForm.expression" name="expression"></v-editor>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </div>

                <el-row>
                    <div>
                        <el-button  icon="d-arrow-left" @click="gotoBack"></el-button>
                        <el-button type="primary" icon="circle-check" @click.native="editSubmit" :loading="editLoading">{{btnEditText}}</el-button>
                    </div>
                </el-row>

            </el-form>
           <!-- <div slot="footer" class="dialog-footer">
                <el-button @click.native="hideDialog"></el-button>
                <el-button type="primary" @click.native="editSubmit" :loading="editLoading">{{btnEditText}}</el-button>
            </div>-->
        <!--</el-dialog>-->

    </div>

</template>

<script>
    import { mapState } from 'vuex'
    import Editor from '../../componnets/wangEditor/Editor.vue' //
    import rule from '../../api/ruleManager'   //API---
    import productList from '../../api/productList' //API
    import devSetting from '../../pages/ruleManager/rule.devSettings.vue'  //
    import resSetting from '../../pages/ruleManager/rule.resSettings.vue'  //

    export default {
        components: {
            'v-editor': Editor,
            devSetting,
            resSetting
        },
        data() {
            var ValidatorTypes = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                }else {
                    callback();
                }
            };
            var ValidatorNumber = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else if(!this.$regExp.number.test(value)){
                    callback(new Error(''));
                } else {
                    callback();
                }
            };
            var ValidatorWeight = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else if(!this.$regExp.number.test(value)){
                    callback(new Error(''));
                } else {
                    callback();
                }
            };
            var ValidatorExpression = (rule, value, callback) => {
                if (value === ''|| value === '<p><br></p>') {
                    callback(new Error(''));
                } else {
                    callback();
                }
            };
            return {
                HShow:'', //
                inputContent: '',  // 
                uploadUrl: '',  // 

                imageUrl:'',
                editFormVisible: false,//
                editFormTtile: '',//

                sid:'',
                truesid:true,
                watchFlag:false, //
                //
                types:'',
                editForm: {
                    id: 0,
                    name: '',
                    code: '',
                    expression:'',
                    smallTypes:'',
                    weight:'',
                    notes:'',
                    enabled: '',// 
                },
                options: [], //
                subsetoptions:[], //
                editLoading: false,
                btnEditText: '',

                editFormRules: { //

                    name: [
                        { required: true, message: ''}
                    ],
                    code: [
                        {required: true, validator: ValidatorNumber}
                    ],
                    weight: [
                        {required: true, validator: ValidatorWeight}
                    ],
                    types: [
                        { required: true, message: ''}
                    ],
                    smallTypes: [
                        { required: true, message: ''}
                    ],
                    enabled: [
                        { required: true, message: '' }
                    ],
                    expression:[
                        {required: true, validator:ValidatorExpression}
                    ],
                    notes:[
                        { mim:0, max:200, message: '200'}
                    ]

                },
                boxLoading:false,
            };
        },
        watch: {
            'editForm.types'(curVal,oldVal){
                if(curVal==''){
                    this.watchFlag = false;
                    return;
                }else{
                    this.watchFlag = true;
                    this.editForm.smallTypes='';
                    this.getsmallTypesoptions(curVal);
                }
            },
            'editForm.expression'(curVal,oldVal){
                if(curVal=='<p><br></p>'){
                    return;
                }else{
                    var _this = this;
                    _this.editForm.expression=curVal;
                    setTimeout(() =>{
                        _this.$refs.editForm.validateField( 'expression');
                },1)
                }

            }

        },
        created(){
            this.$bus.$off('ruleDialogShow');
            this.$bus.$on('ruleDialogShow', (title,data) => {
                this.ruleDialogShow(title,data);
            });
            this.HShow = this.RuleSetting;
        },
        methods: {
            gotoBack(){
                this.$router.push({ path: '/productList/ruleSet',query: { id:this.$route.query.productId}});
            },
            hideDialog(){
               this.editFormVisible = false;
                setTimeout(() =>{
                    this.$refs.editForm.resetFields();
                this.inputContent='<p><br></p>';
                this.editForm.expression='<p><br></p>';
                },100)

            },
            //dialog
            ruleDialogShow(title,data){
                this.editFormVisible = true;
                this.editFormTtile = title;
                if(data.id){
                    this.reload(data);
                }
            },
            reload(data){
                var _this = this;
                _this.boxLoading = true;
                //
                productList.ruleInfo(data).then((res) => {
                    this.editForm = res;
                    this.inputContent=res.expression;
                    this.truesid=true;
                    this.sid = res.smallTypes;
                    this.editForm.types =parseInt(res.types);
                    this.editForm.enabled =String(res.enabled);
                    _this.boxLoading = false;
                    //alert(_this.watchFlag);
                    if(_this.watchFlag){
                        _this.getsmallTypesoptions(_this.editForm.types);
                    }

                    //
                    if(res.conditionDataArr && res.conditionDataArr.length > 0){
                        _this.$refs.child1.setData(res.conditionDataArr); //
                    }

                //this.editForm.enabled =res.enabled;

            }).catch(function (error) {
                    _this.$Error(_this,error);
                    _this.boxLoading = false;
                });

            },
            getsmallTypesoptions(id){//
                //debugger;
                var _this = this;
                var params = {
                    type:'RULE',
                    parentId:id
                }
                rule.getoTypesptions(params).then(res=>{
                    if(res.code=="-1"){
                    _this.$notify({
                        title: '',
                        message: res.msg,
                        type: 'error'
                    });
                }else{
                    _this.subsetoptions = res;
                }
                if(_this.sid&&_this.truesid){
                    _this.truesid=false;
                    _this.editForm.smallTypes =parseInt(_this.sid);
                }
            })
            .catch(function (error) {
                    _this.$Error(_this,error);
                })
            },
            getoTypesptions(){//
                var params = {
                    type:'RULE',
                    parentId:'-1'
                }
                rule.getoTypesptions(params).then(res=>{
                    if(res.code=="-1"){
                    this.$notify({
                        title: '',
                        message: res.msg,
                        type: 'error'
                    });
                }else{
                    this.options = res;
                }

            }).catch(function (error) {
                    this.$Error(this,error);
                })
            },
            //
            editSubmit: function () {
                var _this = this;
                var devData = []; //   -- conditionData
                var devResult = ''; //   -- conditionExpression
                var devValid = _this.$refs.child1.validate(); // ,true

                if(devValid){
                    devResult = _this.$refs.child1.toChange();
                    devData = _this.$refs.child1.getData();
                }else{
                    _this.$message.warning('');
                    return ;
                }
                _this.$refs.editForm.validate((valid) => {
                    if (valid) {
                        _this.editLoading = true;
                        _this.btnEditText = '';

                        //
                        var params = {
                            id: _this.editForm.id,
                            name: _this.editForm.name, //
                            expression: _this.editForm.expression, //
                            notes:_this.editForm.notes, //
                            weight:_this.editForm.weight, //
                            enabled: _this.editForm.enabled, //
                            conditionDataArr:devData,
                            conditionExpression:devResult
                        };
                        productList.instance_put(params).then((res) => {
                            _this.editLoading = false;
                        _this.btnEditText = '';
                        _this.editFormVisible = false;
                        _this.$message({
                            message: "",
                            type: 'success'
                        });
                        _this.init();
                        //_this.$bus.$emit('H_ruleSetList_reload');
                    }).catch(function (error) {
                            _this.editLoading = false;
                            _this.btnEditText = '';
                            _this.$Error(_this,error);
                        });


                    }
                });

            },
            init(){
                this.getoTypesptions();
                if(this.$route.query.id){
                    this.reload({id:this.$route.query.id});
                }
            },
        },
        mounted() {
           this.init();
        },
        computed: {
                ...mapState([
                    'RuleSetting'
                ])
        },
    };
</script>

<style scoped>
    .line{ text-align: center; font-size: 14px;}
    .h-rule-edit-box{position: relative; width: 1000px; margin-bottom: 20px; margin-top: 20px; padding: 10px; border: 1px solid #ddd;}
    .h-rule-edit-title{position: absolute; left: 10px; top: -20px; background: #fff; padding: 8px 5px; font-size: 15px; font-weight: bold; color: #333;}
    .line{ text-align: center; font-size: 14px;}
    .h-ruleInput{width: 240px;}
    .h-ruleInputL{width: 450px;}
</style>



