<template>
	<section>
		<el-row>
			<el-col :span="19">
				<div class="h-scoreSetTitle">
					<strong></strong> {{result.name}}

					<!---->
                    <span v-show="initData.show">
                       <!-- <span>
                            <strong style="margin-left: 15px;"></strong> {{initData.stateName}}
                        </span>-->
						<span v-if="initData.isCG">
                            <span style="margin-left: 15px;">
                                <el-badge is-dot class="item">
                                    <span style="display: inline-block; padding: 5px 8px; border-radius: 2px;  background-color: #eef1f6;">
                                        <span style="color: #ff4949">{{initData.stateName}}</span>
                                    </span>
								</el-badge>
                            </span>
                        </span>
						<!--ID-->
                        <span v-if="initData.isCGAndID">
                            <strong style="margin-left: 15px;"></strong> {{initData.lastUpdateTime}}
                            <el-button type="primary" icon="view"  style="margin-left: 15px;" @click="gotoRelease"></el-button>
                         </span>
						<!--1id2id-->
                        <span v-else>
                             <strong style="margin-left: 15px;">{{initData.stateName}}</strong> {{initData.lastUpdateTime}}
                        </span>
                    </span>

					<el-button type="primary" icon="setting"  style="margin-left: 15px;" @click="gotoPath"></el-button>
				</div>
			</el-col>
			<el-col :span="5">
				<el-input
						placeholder="" icon="search" v-model="searchinput" :on-icon-click="handleIconClick">
				</el-input>
			</el-col>
		</el-row>

		<div class="h-boxLoading" v-loading="boxLoading">
			<!---->
			<scoreSetList></scoreSetList>

			<!------>
			<scoreSetEdit></scoreSetEdit>

			<!-- ---->
			<scoreSettingDialog></scoreSettingDialog>
		</div>


		<el-row class="h-scoreSet-btnBox">
			<el-button  icon="d-arrow-left" @click="gotoBack" :disabled="backDisabled"></el-button>
			<el-button type="primary" icon="document" @click="submit(0)" :loading="editLoading" :disabled="editDisabled"></el-button>
			<el-button type="primary" icon="check" @click="submit(1)" :loading="addLoading" :disabled="addDisabled"></el-button>
			<el-button v-if="initData.isCGAndID" type="primary" icon="setting" @click="resetForm" :loading="resetLoading" :disabled="resetDisabled"></el-button>
		</el-row>
	</section>
</template>

<script>
	import scoreSetList from './scoreSet.list.vue'      //
	import scoreSetEdit from './scoreSet.subAdd.vue'    //
	import scoreSettingDialog from './scoreSet.settingDialog.vue'  //
	import productList from '../../api/productList'   //API

	export default {
		components: {
			scoreSetList,scoreSetEdit,scoreSettingDialog
		},
		data() {
			return {
				id:'',
				result:{
					"id": '',
					"name": ' ',
					"extCode":'',
					"innerCode": '',
					"modelId": '',
					"ownerId": ''
				},
				initData:{
					show:false,
					first:true, //
					isCG:true,  //
					isCGAndID:false,
					stateName:'',
					productName:'',
					lastUpdateTime:'', //
					releaseModelId:'' //id
				},
				oldRuleIds:'',
				boxLoading:true,
				editLoading:false,
				addLoading:false,
				resetLoading:false,
				editDisabled:false,
				addDisabled:false,
				resetDisabled:false,
				backDisabled:false,
				searchinput: '',
				productName:'-'
			}
		},
		methods: {
			//
			handleIconClick(ev) {
				this.$bus.$emit('H_scoreSetList_search',{codeOrName:this.searchinput});
			},
			handleAddRule(){
				var data = {};
				this.$bus.$emit('ruleDialogShow','',data);
			},
			gotoPath(){
				this.$router.push({ path: '/productList/scoreSet-result', query: { name: 'router',id:this.$route.query.id }});
			},
			//
			gotoBack(){
				this.$router.push({ path: '/productList'});
			},
			//
			gotoRelease(){
				this.$router.push({ path: '/productList/scoreSet/release',query:{productId:this.$route.query.id,modelId:this.initData.releaseModelId}});
			},
			getProductInfo(){
				var _this = this;
				if(_this.$Utils.isNumber(_this.id)){
					productList.productInfo_getById(_this.id).then( res => {
						_this.result = res || _this.result;
				}).catch( error =>{
						_this.$Error(_this,error);
				});
				}
			},
			dealSubmit(obj,params,msg){
				var _this = obj;
				productList.factSet_postForm(params).then( res =>{
					//debugger;
					_this.addLoading = false;
				_this.editLoading = false;
				_this.resetDefaultBtn();
				_this.$message({
					type: 'success',
					message: msg
				});
				setTimeout(function(){
					_this.$bus.$emit('H_scoreSetList_reload');
				},500)
			}).catch(error =>{
					_this.$Error(_this,error);
				_this.addLoading = false;
				_this.editLoading = false;
				_this.resetDefaultBtn();
			});
			},
			submit(status){
				var _this = this;
				this.$bus.$emit('H_scoreSetList_getUnSubmitIds',function(ids){

					if(ids){
						var params = {
							productId:_this.result.id,
							status:status,
							factIds:ids  // ID,
						};
					}else{
						var params = {
							productId:_this.result.id,
							status:status
						};
					}

					var msg = status =='1'? '':'';
					if(status=='1'){

						_this.$confirm('', '', {}).then(() => {
							_this.addLoading = true;
						_this.disabledOthersBtn('addDisabled');
						_this.dealSubmit(_this,params,msg);
					});
					}else{
						_this.editLoading = true;
						_this.disabledOthersBtn('editDisabled');
						_this.dealSubmit(_this,params,msg);
					}




				});



			},

			resetDefaultBtn(){ //
				this.editDisabled = false;
				this.addDisabled = false;
				this.backDisabled = false;
				this.resetDisabled = false;
			},
			disabledAllBtn(){ //
				this.editDisabled = true;
				this.addDisabled = true;
				this.backDisabled = true;
				this.resetDisabled = true;
			},
			disabledOthersBtn(btn){ //
				this.disabledAllBtn();
				this[btn] = false;
			},
			resetForm(){
				var _this = this;
				_this.$confirm('', '', {}).then(() => {
					_this.resetLoading = true;

				_this.disabledOthersBtn('resetDisabled');
				productList.productReset({productId:_this.result.id}).then(res =>{
					if(res.code=='0'){
					_this.$message({
						type:'success',
						message: ''
					})
					setTimeout(function(){
						_this.$bus.$emit('H_scoreSetList_reload');
					},800);


				}else{
					_this.$message({
						type:'warning',
						message:res.msg || ''
					})
				}
				_this.resetLoading = false;
				_this.resetDefaultBtn();
			}).catch(error =>{
					_this.$Error(_this,error);
				_this.resetLoading = false;
				_this.resetDefaultBtn();
			});
			});


			},
			render(params){

				//_this.$bus.$emit('H_ruleSetInfo',{status:res.status,productName:res.productName,lastUpdateTime:res.lastUpdateTime,releaseModelId:res.releaseModelId});

				//debugger;

				if(params.status !=null){ //
					this.initData.first = false;
					this.initData.show = true;
					if(params.status =='0'){  //
						this.initData.isCG = true;
						this.initData.stateName = '';
						if(params.releaseModelId){ //
							this.initData.isCGAndID = true;
						}else{
							this.initData.isCGAndID = false;
						}
					}else{
						this.initData.isCG = false;
						this.initData.isCGAndID = false;
						this.initData.stateName = '';
					}

				}else{
					this.initData.first = true;
					this.initData.show = false;
					this.initData.isCG = false;
					this.initData.isCGAndID = false;
				}
				this.initData.productName = params.productName; //
				this.initData.lastUpdateTime = params.lastUpdateTime; //
				this.initData.releaseModelId = params.releaseModelId; //id


			},

			stopLoading(){
				this.boxLoading = false;
			},
			showLoading(){
				this.boxLoading = true;
			},

		},
		created(){
			this.$bus.$off('H_scoreSetInfo');
			this.$bus.$on('H_scoreSetInfo',(params)=>{
				this.render(params);
			});

			this.$bus.$off("H_SCORESET_showLoading");
			this.$bus.$on("H_SCORESET_showLoading",()=>{
				this.showLoading();
		});
			this.$bus.$off("H_SCORESET_hideLoading");
			this.$bus.$on("H_SCORESET_hideLoading",()=>{
				this.stopLoading();
		});
		},
		mounted(){
			var _this = this;
			this.id = this.$route.query.id;
			this.getProductInfo();
			/*setTimeout(() =>{
				_this.$bus.$emit('H_scoreSetList_getRuleIds',function(ids){
				_this.oldRuleIds = ids;
			});
			},1000)*/
		}
	}
</script>

<style scoped>
	.h-scoreSetTitle{font-size: 15px;}
	.button_box span{ color: #bebfc0;}
	.btn_border_none{ border:none; background: none;}
	.rule_box{ margin-top: 40px;}
	.c_ff0000{ color: #ff0000;}
	.h-scoreSet-btnBox{margin-top: 30px; }
</style>



